          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="12" class="text-right">
        <el-button type="primary" @click="sendEditUser()" round>Speichern</el-button>
      </el-col>
      <el-col class="text-center mt-3" :span="24"> 
               <strong :class="editUserItems.isActive ? 'text-success':'text-danger'">{{editUserItems.isActive ? 'Aktiv':'Passiv'}} </strong>
