      </div> -->
    </el-row>
  </b-modal>
</template>

<script>
import { mapGetters } from 'vuex';
import { putApi, postApiHelper, postApiCreateUser } from '../../../api/api';
const authorityOptions = [
  "Auftrag anlegen",
  "Auftrag stornieren",
  "Belegnr. ändern",
  "Beleg ändern"
];
const authorityOptionsLager = [
  "Lager Login",
  "Lager Schreibrechte",
  "Lager Artikelverwaltung",
  "Lager zurück auf Ablage",
  "Lager Storno Beleg"
];
export default {
  props: ["editUserItems"],
  data() {
    return {
      checkAll: false,
      checkedAuthority: false,
      LagercheckedAuthority: [],
      authority: authorityOptions,
      isIndeterminate: true,
      checkAllLager: false,
      checkedAuthorityLager: [],
      authorityLager: authorityOptionsLager,
      isIndeterminateLager: true,
      user: {
        name: "",
        lastname: "",
        username: "",
        password: ""
      },
      options: [
        {
          value: "PP",
          label: "PP"
        },
        {
          value: "Alex",
          label: "Alex"
        },
        {
          value: "Treptow",
          label: "Treptow"
        },
        {
          value: "EG",
          label: "EG"
        },
        {
          value: "Potsdam",
          label: "Potsdam"
        },
        {
          value: "MZ",
          label: "MZ"
        },
        {
          value: "ESM",
          label: "ESM"
        },
        {
          value: "Köpenick",
          label: "Köpenick"
        },
        {
          value: "GB",
          label: "GB"
        },
        {
          value: "Steglitz",
          label: "Steglitz"
        },
        {
          value: "EPK",
          label: "EPK"
        },
        {
          value: "EPB",
          label: "EPB"
        }
      ],
      value: "",
      password: ""
    };
  },
  computed:{
    ...mapGetters(['getBranch'])
  },
  watch: {
    editUserItems() {
      this.checkedAuthority = [];
      this.checkedAuthorityLager = [];
      // // console.log(this.editUserItems);
      if (this.editUserItems.isAnlegen) {
        this.checkedAuthority.push("Auftrag anlegen");
      }
      if (this.editUserItems.isStornieren) {
        this.checkedAuthority.push("Auftrag stornieren");
      }
      if (this.editUserItems.isBelegNrAndern) {
        this.checkedAuthority.push("Belegnr. ändern");
      }
      if (this.editUserItems.isBelegAndern) {
        this.checkedAuthority.push("Beleg ändern");
      }
      if (this.editUserItems.isLagerLogin) {
        this.checkedAuthorityLager.push("Lager Login");
      }
      if (this.editUserItems.isLagerschreiben) {
        this.checkedAuthorityLager.push("Lager Schreibrechte");
      }
      if (this.editUserItems.isLagerArtikel) {
        this.checkedAuthorityLager.push("Lager Artikelverwaltung");
      }
      if (this.editUserItems.isLagerZuruck) {
        this.checkedAuthorityLager.push("Lager zurück auf Ablage");
      }
      if (this.editUserItems.isLagerstornoBeleg) {
        this.checkedAuthorityLager.push("Lager Storno Beleg");
      }
    }
  },
  methods: {
        putUserPermission(id,permissionData){
      putApi('user',id,permissionData).then(result=>{
        if(result.result==1){
          setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Der Benutzer wurde erfolgreich aktualisiert.",
          type: "success"
        });
        })
        }
      })
      
    },
    sendEditUser() {

      // // console.log(this.editUserItems);
    if(this.password!=""){
        postApiHelper('password',{ password : this.password }).then(result=>{
        // console.log(result.result);
           putApi('user',this.editUserItems.id,{ name : this.editUserItems.name, lastname : this.editUserItems.lastname, username : this.editUserItems.username, password : result.result }).then(result=>{
        if(result.result==1){
           postApiCreateUser({ userId : this.editUserItems.id ,password : this.password }).then(result=>{
             // console.log(result);
             setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Der Benutzer wurde erfolgreich aktualisiert.",
          type: "success"
        });
        })
              this.$emit("editedUser", this.editUserItems);
      this.hide("editUserModal");
           })
              
        }
      })
      })
    }else{
       putApi('user',this.editUserItems.id,{ name : this.editUserItems.name, lastname : this.editUserItems.lastname, username : this.editUserItems.username, password : this.editUserItems.password }).then(result=>{
        if(result.result==1){
              setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Der Benutzer wurde erfolgreich aktualisiert.",
          type: "success"
        });
        })
        this.password=""
              this.$emit("editedUser", this.editUserItems);
      this.hide("editUserModal");
        }
      })
    }
   

    },
    hide(hideModal) {
      this.$refs[hideModal].hide();
    },
    handleCheckAllChange(val) {
      this.checkedAuthority = val ? authorityOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.authority.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.authority.length;
    },
    handleCheckAllChangeLager(val) {
      this.checkedAuthorityLager = val ? authorityOptionsLager : [];
      this.isIndeterminateLager = false;
    },
    handleCheckedCitiesChangeLager(value) {
      let checkedCount = value.length;
      this.checkAllLager = checkedCount === this.authorityLager.length;
      this.isIndeterminateLager =
        checkedCount > 0 && checkedCount < this.authorityLager.length;
    }
  },
  created(){
    this.checkedAuthority = [];
      this.checkedAuthorityLager = [];
      // // // console.log(this.editUserItems);
      if (this.editUserItems.isAnlegen) {
        this.checkedAuthority.push("Auftrag anlegen");
      }
      if (this.editUserItems.isStornieren) {
        this.checkedAuthority.push("Auftrag stornieren");
      }
      if (this.editUserItems.isBelegNrAndern) {
        this.checkedAuthority.push("Belegnr. ändern");
      }
      if (this.editUserItems.isBelegAndern) {
        this.checkedAuthority.push("Beleg ändern");
      }
      if (this.editUserItems.isLagerLogin) {
        this.checkedAuthorityLager.push("Lager Login");
      }
      if (this.editUserItems.isLagerschreiben) {
        this.checkedAuthorityLager.push("Lager Schreibrechte");
      }
      if (this.editUserItems.isLagerArtikel) {
        this.checkedAuthorityLager.push("Lager Artikelverwaltung");
      }
      if (this.editUserItems.isLagerZuruck) {
        this.checkedAuthorityLager.push("Lager zurück auf Ablage");
      }
      if (this.editUserItems.isLagerstornoBeleg) {
        this.checkedAuthorityLager.push("Lager Storno Beleg");
      }
  }
};
</script>

