<script>
import auth from "../mixins/auth";
import isAdmin from "../mixins/isGod";
import eralpForm from '../components/eralpComponents/eralp-form'
import { mapGetters } from "vuex";
import { dateFormatterElement, dateFormatter } from "../dateFormatter";
import exportPdfTruck from '../components/exportPdf/exportPdfTruck'
import { putApi } from '../api/api';
export default {
  mixins: [auth, isAdmin],
  components:{
    exportPdfTruck,
    eralpForm
  },
  data() {
    return {
      customerDetails:[],
      newDriver:{
        driverName :"" 
      },
      selectedManualTruck:null,
      selectedPdfData:[],
      editTruck:{
        truckName:"",
        index:null
      },
      multiple: [],
      dragIndexCustomer:null,
      pickerOptions: {
        shortcuts: [
          {
            text: "Today",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Yesterday",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "A week ago",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      loading:true,
      errPoint:[],
      multipleSelection: [],
      image: require("@/assets/img/truck.png"),
      checked: [],
      selectedTruck: [],
      selectedTruckId: null,
      multi: [],
      groupTrucks: [],
      customer: [],
      alltrucks: [],
      index: null,
      i:null,
      dragType:null,
      customerIndex: [],
      date: null,
      selectedDate: null,
      hideHeader: true,
      truckSelectedItem:{id:1,truckName:"a"},
      customerManualIndex:null,
      truckIndex:null,
      trucks:[]
    };
  },
  computed: {
    ...mapGetters(["getCustomers", "getTrucks", "getTrucksItem","getTruckOrder"])
  },
  watch: {
    date(data){
          this.getTrucks.forEach(result=>{
            result.disabled=false
          })
          // console.log(data);
           this.$store.dispatch('initOrdersForTruck',data).then(res=>{
          if(res=='success'){
            // console.log(res);
            if(this.getTruckOrder!=null){
              // console.log('null değil girdi');
              this.$store.state.trucks.trucksItem=[]
         if(this.getTruckOrder.length!=0 ){
       
          // console.log('girdi');
           
            this.getTruckOrder.forEach(order=>{
                // // console.log(order.truckId);
              
              if(order.truckId!=0){
                // // console.log('0 DEĞİL',order.truckId);
                let truckName=null
                let data=[]
                data.push(order)
                this.alltrucks.forEach(truck=>{
                  if(truck.id==order.truckId){
                    // // console.log(this.alltrucks.indexOf(truck));
                    this.alltrucks[this.alltrucks.indexOf(truck)].disabled = true
       //           this.alltrucks.splice(this.alltrucks.indexOf(truck),1)
                    truckName = truck.name
                  }
                })
                   this.$store.dispatch('sendTruckOrdersItem',{
             date : order.setupDate,
              truckId : order.truckId,
             truckIndex: 'Tour '+(order.truckId),
             truckName : truckName,
               data : data
      }).then(result=>{
     //    this.$store.state.trucks.truckOrder.splice(index,1)
      // // console.log(this.i);
      this.$store.state.trucks.truckOrder.splice(this.getTruckOrder.indexOf(order),1)
     // this.$store.state.trucks.trucks.splice(this.i,1)
      })
              }
            })
            }
            }
            else{
              // console.log('null ');
              
             this.$store.commit('removeTruckItem')
            }
   
           
          }
        })
    },
    selectedDate(event) {
      // console.log(event);
      
    if(event==null){
      
    }
    else {
        this.customer = [];
      let date = dateFormatterElement(event);
      let today = dateFormatter(date);
      this.date = date;
    }
    },
    multipleSelection(event) {
      // // console.log(event);

      if (event.length != 0) {
        for (let i = 0; i < this.getTruckOrder.length; i++) {
          if (this.getTruckOrder[i].id == event[event.length - 1].id) {
            this.customerIndex.push(i);
          }
        }
      }
      // // console.log(this.customerIndex);
    },
    selectedManualTruck(event){
      // // console.log("Truck Id : ",event);
      let index;
      let truckName;
      for (let i = 0; i < this.alltrucks.length; i++) {
        if(this.getTrucks[i].id==event){
            // // console.log(this.alltrucks[i].index,this.alltrucks[i]);
            
             index = this.alltrucks[i].id
            truckName = this.alltrucks[i].name
            this.i = i
          
        }
        
      }
      // // console.log(index,truckName);
      this.truckSelectedItem.id =index
      this.truckSelectedItem.truckName = truckName
      // // console.log(this.truckSelectedItem);
      
    }
  },
  methods: {
    showDetails(row){
      this.customerDetails = row
    },
    sendNewDriver(){
    $('#label1').removeClass("borderError")
      if(this.newDriver.driverName==""){
          setTimeout(() => {
            this.$notify({
          title: "Error",
          message: 'ungültig Fehrer',
          type: "error"
        });
        }, 150);
    $('#label1').addClass("borderError")
      }
      else {
      this.$store.dispatch('sendNewDriver',this.newDriver.driverName)
      this.$refs['newDriver'].hide()
      }
      
    },
    selectedPdfDataMethod(){

      this.selectedPdfData = this.getTrucksItem
      
  
    },
   removeItem(truckIndex,dataIndex){
     // // console.log(this.getTrucksItem[truckIndex].data[dataIndex]);
     this.$store.state.trucks.truckOrder.push(this.getTrucksItem[truckIndex].data[dataIndex])
   this.getTrucksItem[truckIndex].data.splice(dataIndex,1)
    
   },
    editTruckName(){
      this.getTrucks[this.editTruck.index].name = this.editTruck.truckName
      this.$refs['editTruck'].hide()
    } ,
    changedSelectManualTruck(index,row){
      // // console.log(index,row);
      let data = []
      data.push(row)
     setTimeout(() => {
       // // console.log(this.truckSelectedItem);
       
        this.$store.dispatch('sendTruckItem',{
        date : this.date,
        truckId : this.truckSelectedItem.id,
        truckIndex: 'Tour '+(this.truckSelectedItem.id),
        truckName : this.truckSelectedItem.truckName,
        data : data
      })
      this.$store.state.trucks.truckOrder.splice(index,1)
      // // console.log(this.i);
      
  //    this.$store.state.trucks.trucks.splice(this.i,1)
  this.alltrucks[this.i].disabled = true
     }, 500);

      
    },
    startedDrag(index,event,dragType,indexTruck){
    // // console.log(index,event,dragType,indexTruck);
      this.dragType = dragType
      this.dragIndexCustomer=index

      
      this.truckIndex = indexTruck
            console.log(this.dragIndexCustomer,this.truckIndex);
    },

    enterDrag(ev){
      
      // // console.log('enter');
      
    },
    leaveDrag(ev){
      // // console.log('leave');
      
    },
    endDrag(ev){
      // // console.log();
      
    },
     dragFinish(to, ev,id) {
      console.log(to,ev,'end',id);
       let orderId ;
     if(this.dragType=="freeItemChange"){
        orderId =  this.getTruckOrder[this.dragIndexCustomer].id
      putApi('orders',orderId, { truckId : id }).then(result=>{
        // // console.log(result);
        
       if(result.result==1){
             this.$store.state.trucks.truckOrder.splice(this.dragIndexCustomer,1)
                   this.getTrucksItem[to].data.push(this.getTruckOrder[this.dragIndexCustomer])
      
       }
      })
   
      
     }
     else {
      orderId =  this.getTrucksItem[this.truckIndex].data[this.dragIndexCustomer].id
 //console.log(this.getTrucksItem[this.truckIndex].data[this.dragIndexCustomer].id);
  
          putApi('orders',orderId, { truckId : id }).then(result=>{
               // // console.log(result);
       if(result.result==1){
             // // console.log('to data=>',this.getTrucksItem[to].data,'to',to, 'Eski Aldığım Deponun Silinecek Itemı dragIndexCustomer',this.dragIndexCustomer, 'Eski Aldığım Depo truckIndex',this.truckIndex)
              this.getTrucksItem[to].data.push(this.getTrucksItem[this.truckIndex].data[this.dragIndexCustomer])
              this.getTrucksItem[this.truckIndex].data.splice(this.dragIndexCustomer,1)
       }
      })
     }
        
    },
    showDate() {
      this.$refs["showDatePicker"].showPicker();
    },
    getIndex(index) {
      // // console.log(index);
    },
    changeTruck(event) {
      let selected;
      let index;
      for (let i = 0; i < this.getTrucks.length; i++) {
        if (this.getTrucks[i].id == this.selectedTruckId) {
          // // console.log(this.getTrucks[i].id, this.selectedTruckId, i);

          this.index = i;
        }
      }
      this.getTrucks.filter(res => {
        if (this.selectedTruckId == res.id) {
          selected = res.name;
        }
      });
      this.selectedTruck = { id: this.selectedTruckId, name: selected };
      // // console.log(this.selectedTruck);
    },
    removeTrucks(index, id) {
      this.$confirm(
        "This will permanently delete the file. Continue?",
        "Warning",
        {
          confirmButtonText: "OK",
          cancelButtonText: "Cancel",
          type: "warning"
        }
      )
        .then(() => {
          this.$store.dispatch("removeTruck", { index: index, id: id });
          this.$message({
            type: "success",
            message: "Delete completed"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "Delete canceled"
          });
        });
    },
    insertTruckItem(index) {
      let date = new Date();
      let today = dateFormatterElement(date);
      today = dateFormatter(today);
      // // console.log(today);

      this.$store.dispatch("sendTruckItem", {
        date: today,
        truckId: this.selectedTruck.id,
        truckIndex: 'Tour '+(index+1),
        truckName: this.selectedTruck.name,
        data: this.multipleSelection
      });
        this.alltrucks[this.index].disabled = true
      this.customerIndex.sort();
      this.customerIndex.reverse();
      // // console.log(this.customerIndex);

      for (let i = 0; i < this.customerIndex.length; i++) {
        // // console.log(this.customerIndex[i], "deleted");

        this.$store.state.trucks.truckOrder.splice(this.customerIndex[i], 1);
      }
      this.customerIndex = [];
      this.$refs.multipleTable.clearSelection();
    },
    toggleSelection(rows) {
      if (rows) {
        // // console.log(rows);

        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // MulipleSelectedPush(row, index, result) {
