    //     }
    //     this.multiple.splice(deleteCheck, 1);
    //     // // console.log('Deleted Id =>', deleteCheck, 'Id =>', id);

    //   }
    // },
    setTruck(id) {
      let trucksItem = [];
      let truck;
      trucksItem = this.$store.getters.getTrucksItem(id);
      this.getTrucks.filter(res => {
        if (res.id == id) {
          truck = res.name;
        }
      });
      this.groupTrucks.push({
        truckId: trucksItem[0].truckId,
        truckName: truck,
        trucks: trucksItem
      });
      // // console.log(this.groupTrucks);
    }
  },
  created() {
  let date = new Date();
    let today = dateFormatterElement(date);
 //  today = dateFormatter(today);
    this.date = today;
     
    // // console.log(this.$refs);
    //  this.customer = this.getCustomers
  },
  mounted(){
    this.$store.dispatch('initTruckTable').then(result=>{
      this.trucks = result.body
      if(result.result=='success'){
       this.alltrucks  = this.getTrucks
    
                setTimeout(() => {
                   this.loading=false
            setTimeout(() => {

                  this.hideHeader = false;
            }, 250);
