            this.getDefinition.forEach(definition=>{             
            if(miniDef.setupDefinitionId == definition.id){
                if( definition.parentId==0){
                  this.pdfData.tarif = definition.name
                  this.pdfData.isWhite= definition.isWhite
                  this.pdfData.description = definition.description
                }
                this.pdfData.options.push(definition)
            }
          })
      })

    }
  },
