      console.log(element,renderer);
      
        return true;
    }
};
doc.fromHTML(elementHTML, 25, 25, {
    'width': 170,
    'elementHandlers': specialElementHandlers
});
 //  doc.line(10, 20, 250, 20) // horizontal line

// Save the PDF

doc.autoPrint();  // <<--------------------- !!
window.open(doc.output('bloburl'), '_blank');
// doc.save(this.pdfData.beleg+'-'+this.pdfData.setupDate+'.pdf');
//       html2canvas(document.querySelector("#pdfExport"), {
//         imageTimeout: 5000,
//         useCORS: true
//       }).then(canvas => {
//         document.getElementById("pdf").appendChild(canvas);
//         let img = canvas.toDataURL("image/png");
//         let pdf = new JsPDF("portrait", "mm", "a4");
//         pdf.addImage(img, "JPEG", 5, 10, 200, 250);
//         pdf.save(this.pdfData.beleg+'-'+this.pdfData.date+'-'+this.pdfData.zeit+"uhr.pdf");
//         document.getElementById("pdf").innerHTML = "";
//       });
    },
  },
  provide: {
    grid: [Toolbar, PdfExport]
  }
