<template>
  <b-modal id="excellExport" ref="excellExport" size="xl" title="Extra Large Modal" hide-footer>
    <el-row>
      {{selectedBranchName}}
      <el-col :span="12" :offset="6">
        <div class="text-center">
          <div class="form-group">
            <strong style="font-weight:700">Datum</strong>
            <el-date-picker
              class="w-100"
              v-model="datePickerElement"
              type="daterange"
              range-separator="To"
              start-placeholder="Start date"
              end-placeholder="End date"
            ></el-date-picker>
          </div>
          <div v-if="getActiveUser!='' ? getActiveUser.isAdmin ? true: false: false" class="form-group mt-3">
            <el-select v-model="selectedBranch" @change="changeCompanyId" placeholder="Select">
              <el-option
                v-for="item in getBranch"
                :key="item.id"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </div>
          <div v-else class="form-group mt-3">
            {{selectedBranchName}}
          </div>
          </div>

      </el-col>
    </el-row>

    <div v-if="selectedBranch!=''">
      <el-table
        class="border shadow p-3"
        :data="filtredTable"
        :default-sort="{prop: 'date', order: 'descending'}"
        style="width: 100%"
      >
        <el-table-column prop="customerName" label="Vorname" sortable></el-table-column>
        <el-table-column prop="customerLastname" label="Nachname"></el-table-column>
        <el-table-column prop="customerAddress" label="Straße" width="400px"></el-table-column>
        <el-table-column prop="beleg" label="Beleg"></el-table-column>

        <el-table-column prop="setupDate" label="Datum"></el-table-column>
        <el-table-column prop="tarif" label="Tarif"></el-table-column>
        <el-table-column prop="department" label="Ware">
                 <template slot-scope="scope">
    {{scope.row.isHaus && scope.row.isAus? 'Mischlager':scope.row.isHaus?'Hauslager':'Außenlager'}}
      </template>
        </el-table-column>
        <el-table-column prop="isOldMachine" label="Altgerat">
                  <template slot-scope="scope">
    {{scope.row.isOldMachine? 'Ja':'Nein'}}
      </template>
        </el-table-column>
      </el-table>
              <div v-if="selectedBranchName!=''" class="text-center mt-4">
          <vue-excel-xlsx
            :data="filtredTable"
            :columns="columns"
            :filename="selectedBranchName"
            :sheetname="selectedBranchName"
            class="btn btn-outline-primary"
          >Download</vue-excel-xlsx>
        </div>
    </div>
  </b-modal>
</template>

<script>
import { dateFormatter, dateFormatterElement, dateFormatterConvertNum } from "../../../dateFormatter";
import { mapGetters } from 'vuex';
export default {
  props: ["tableData"],
  data() {
    return {
      datePickerElement: null,
      dateStart: null,
      dateFinish: null,
      dateStartNum: null,
      dateFinishNum: null,
      selectedBranch: "",
      columns:[
         {
          label: "Vorname",
          field: "customerNmae"
        },
        {
          label: "Nachname",
          field: "customerLastname"
        },
        {
          label: "Straße",
          field: "customerAddress"
        },
         {
          label: "Beleg",
          field: "beleg"
        },
        {
          label: "Tarif",
          field: "tarif"
        },
        {
          label: "Abteilung",
          field: "department"
        }
      ],
      selectedBranchName:""
     
    };
  },
  watch: {
    datePickerElement() {
      let date1 = dateFormatterElement(this.datePickerElement[0]);
      let date2 = dateFormatterElement(this.datePickerElement[1]);
      this.dateStart = dateFormatter(date1);
      this.dateFinish = dateFormatter(date2);
     this.dateStartNum =  dateFormatterConvertNum(date1)
     this.dateFinishNum =  dateFormatterConvertNum(date2)
    },
    selectedBranch() {
      this.showTable = true;
    }
  },
  computed: {
    filtredTable() { 
      return this.tableData.filter(response => { 
          let[date,month,year] = response.setupDate.split('.')
          let resultDate = year+'-'+month+'-'+date
          let convertNumDate = dateFormatterConvertNum(resultDate)
            
        if (this.selectedBranch == response.companyId && this.dateStartNum < convertNumDate && convertNumDate < this.dateFinishNum) {
          return response;
        }
      });
    },
    ...mapGetters(['getActiveUser',"getBranch"])

  },
  methods:{
    changeCompanyId(data){
      this.getBranch.forEach(response=>{
        if(response.id==data){
          this.selectedBranchName = response.name
        }
      })
// 
      
    }
  },
  mounted(){
    setTimeout(() => {
    if(this.getActiveUser.length!=0){
        if(!this.getActiveUser.isAdmin){
      this.selectedBranch = this.getActiveUser.companyId
      this.selectedBranchName = this.getActiveUser.companyName
    }
    }
    }, 1000);
  }
};
</script>

