<script>
import newBranchModal from '../components/Modals/newBranchModal/newBranchModal'

import { mapGetters } from 'vuex'
import isAdmin from '../mixins/isGod'
import auth from '../mixins/auth'
import { getApi } from '../api/api'
export default {
  components:{
    newBranchModal,
  },
computed:{
    ...mapGetters(['getBranch'])
},
data(){
  return {
    editBranchItems :[],
    drawer:false,
    errPoint:[],
    premiumBranch:[],
    normallyBranch:[],
    branches:[]
  }
},
  mixins:[auth,isAdmin],
methods:{
  editBranch(row){
    this.editBranchItems = row
    this.drawer = true
  },
  sendEditBranch(){
     if(this.editBranchItems.label == "")
      {
                 setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Filiale",
          type: "error",
            duration: 7000
        });
       }, 100);
        this.errPoint.push({ err: "Filiale" });
      }
      else {
      this.editBranchItems.value = this.editBranchItems.label 
      this.drawer = false
      }
  },
  removeBranch(row){
     this.$confirm('Möchten Sie dies wirklich entfernen?')
          .then(_ => {
            row.isActive = false
            done();
          })
          .catch(_ => {});
    
  }
},
mounted(){
  getApi('companyRight?limit=30').then(result=>{
    let body = result.body
    body.forEach(data=>{
        this.getBranch.forEach(res=>{
          if(data.companyId == res.id){
              this.branches.push({...data,...res })
          }
  })
    })
// console.log(this.branches);

  })
  
}
