import Vue from 'vue'
import Vuex from 'vuex'
import { cryptoData } from '../../secure';
import {getApi, postApi, postApiHelper , postApiCreateUser} from '../../api/api'

Vue.use(Vuex);

const state={
    users: [],
      isAuth:false,
      activeUser:[],
      tokenNa:null,
      userPassword:"",
      tokenHa:null
}
const getters= {
  getUserPassword(state){
    return state.userPassword;
  },
getUsers(state){
    return state.users;
},
getTokenNa(state){
  return state.tokenNa;
},
getTokenHa(state){
  return state.tokenHa;
},
getIsAuth(state){
  return state.isAuth;
},
getActiveUser(state){
  return state.activeUser 
}
}
const mutations= {
  setUserTable(state,payload){
    state.users = payload.body
  },
setToLogin(state,payload){
  state.isAuth = true,
  state.activeUser = payload
  const tokenNa = payload.tokenNa
  const tokenHa = payload.tokenHa
  state.tokenNa = payload.tokenNa
  state.tokenHa = payload.tokenHa
  // console.log(payload.token);
  
 // localStorage.setItem('token','eralpsoftware')
 // localStorage.setItem('tokenId',JSON.stringify(payload))
 let tokenCrypto = cryptoData(tokenNa)
 let tokenCryptoHa = cryptoData(tokenHa)
 localStorage.setItem('token_N_a',tokenCrypto)
 localStorage.setItem('token_H_a',tokenCryptoHa)
 let userCrypto = cryptoData(JSON.stringify(payload))
 localStorage.setItem('tokenId',userCrypto)

},
setActiveUser(state,payload){
  // console.log(payload);
  
  state.activeUser  =payload
},
hasToken(state,payload){
  state.isAuth = payload

  
},
setNewUser(state,payload){
  state.users.unshift(payload)
},
}

const actions= {
  initUserTable(VuexContext){

   return getApi('user?limit=150').then(user=>{
     VuexContext.commit('setUserTable',user)
      return 'success'
   }).catch(err=>{
     // console.log(err);
     return 'err'
   })
    
  },
  sendNewUser(VuexContext,payload){
    //APİ İŞLEMLERİ
   return postApiHelper('password',{password : payload.password}).then(result=>{
      // console.log(result);
      let dontHashPass=payload.password
      VuexContext.state.userPassword = payload.password
      payload.password = result.result
      return  postApi('user',payload).then(result=>{
          if(result.result!=null){
             return postApiCreateUser({userId : result.result , password :dontHashPass }).then(result=>{
               // console.log(result);
               
              if(result.status=='success'){
                VuexContext.commit('setNewUser',payload)
                 
            return 'success'
              }
            })
       
          }
        })
    })
    
    

  },
 toLogin(VuexContext,payload){
   let user = VuexContext.state.users;
   let loginUser = 'fail'
 
   let companyName =""
    user.filter(response =>{
      if(response.username == payload.username && response.isActive)
      {

       if(response.companyId==0){
         companyName = "All"
       }
       else {
        VuexContext.rootState.branch.company.forEach(company=>{
          if(response.companyId==company.id){         
            companyName = company.name
          }
        })
       }
        VuexContext.commit('setToLogin',{...response,companyName:companyName,tokenNa:payload.tokenNa, tokenHa:payload.tokenHa})
        loginUser = "success"
        
      }
    })
    return loginUser
 },
 hasToken(VuexContext,payload){
  VuexContext.commit('hasToken',payload)
VuexContext.commit('setCustomersForBranch', VuexContext.state.activeUser)
 
  
 },
 toLogout(VuexContext){
 localStorage.clear()
  VuexContext.state.isAuth = false
  VuexContext.rootState.customers.customersAll=[]
  VuexContext.state.activeUser = []
   return 'Logout'
 }
}

export default{
    state,
    getters,
    mutations,
    actions
}