<script>
import { dateFormatter } from "../dateFormatter";
import newUserModal from "../components/Modals/newUserModal/NewUserModal";
import editUserModal from "../components/Modals/editUserModal/editUserModal";
import { mapGetters } from 'vuex';
import auth from '../mixins/auth';
import isAdmin from '../mixins/isGod';
import { putApi } from '../api/api';
export default {
  components: {
    newUserModal,
    editUserModal
  },
  mixins:[auth,isAdmin],
    data() {
    return {
      lager: "All",
      editedUser: [],
      editUserIndex: null,
      editStatus: false,
      category: false,
      selectedBranch: "",
      searchBox: "",
      search: { type: "name", label: "Vorname" },
      datePickerElement: null,
      editUserItems: [],
      filtredName: null
    };
  },
  computed: {
      ...mapGetters(['getBranch','getUsers']),
    filtred() {
      if (this.lager != "All") {
        return this.getUsers.filter(response => {
          if (response[this.lager]) {
            if (this.selectedBranch == "") {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            } else {
              if (response.companyId == this.selectedBranch) {
                return String(response[this.search.type])
                  .toLowerCase()
                  .match(this.searchBox.toLowerCase());
              }
            }
          }
        });
      } else {
        if (this.selectedBranch == "") {
          if (this.searchBox == "") {
            return this.getUsers;
          } else {
            return this.getUsers.filter(response => {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            });
          }
        } else {
          return this.getUsers.filter(response => {
            if (response.companyId == this.selectedBranch) {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            }
          });
        }
      }
    },
    filtredBranch() {
      if (this.lager != "All") {
        return this.getUsers.filter(response => {
          if (response[this.lager]) {
            if (this.selectedBranch == "") {
              return response;
            } else {
              if (response.companyId == this.selectedBranch) {
                return response;
              }
            }
          }
        });
      } else {
        if (this.selectedBranch == "") {
          return this.getUsers;
        } else {
          return this.getUsers.filter(response => {
            if (response.companyId == this.selectedBranch) {
              return response;
            }
          });
        }
      }
    }
  },
  methods: {
    putUserPermission(id,permissionData){
      putApi('user',id,permissionData).then(result=>{
        if(result.result==1){
          setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Der Benutzer wurde erfolgreich aktualisiert.",
          type: "success"
        });
        })
        }
      })
      
    },
    searchingUser(type) {
      if (type == "username") this.search = { type: "username", label: "Login" };
      if (type == "name") this.search = { type: "name", label: "Vorname" };
      if (type == "lastname")
        this.search = { type: "lastname", label: "Nachname" };
    },
    editUser(row,index) {
      //  let num = this.tableData.length
      //   num = num-1
      this.editUserIndex = index;
      this.editUserItems = row;
    }
  },

  watch: {
    editedUser(){
      // console.log(this.editedUser,'worked');
      
      this.getUsers[this.editUserIndex] = this.editedUser
    },
    selectedBranch() {
      // console.log(this.selectedBranch);
    }
  }
};
</script>
