<template>
  <div v-if="!loading">
    <el-row  class="mt-5">
      <el-col :span="12"
        >
       
        <div>
          <input v-model="searchProductInWarehouse" type="text" class="rounded-pill p-3 border bg-light w-75 px-5" placeholder="Artikel Suchen"> 
          <el-button :disabled="searchProductInWarehouseItems.length==0 && searching" type="primary" round @click="searching=true">{{'Suchen'}}</el-button>
        </div>
        
        </el-col
      >
      <el-col v-if="getActiveUser.isLagerSchreiben" :span="12">
        <el-button :disabled="searching" type="primary" v-b-modal.newLagerProduct round>
          <i class="fas fa-plus"></i> Artikel Hinzufügen</el-button
        >
      </el-col>
    </el-row>
  <div class="p-5">
        <div class="text-center">
        <el-button
        :disabled="searching"
            class="my-2"
            @click="newStellplatz()"
            type="success"
            round
          >
            <i class="fas fa-plus"></i> Neu Stellplatz</el-button
          >
    </div>
    <el-collapse  v-model="activeNames" v-if="!searching">
  <el-collapse-item 
      :id="lager.id"
    :name="lager.id"
  class="p-3 border bg-light shadow my-3 bg-success"
      v-for="(lager, index) in getLagers"
      :key="index" >
      <template slot="title">
      <div @click="getLagerItems(lager.id)" class="text-left w-100 p-3 bg-light">
          <span>Stellplatz {{ lager.stellplatz }} - Gang {{lager.gang}}</span>
      </div>
      </template>
           <div  v-if="getActiveUser.isLagerSchreiben" class="text-center bg-light">
          <el-button
            class="my-2"
            @click="getLagerIndex(lager.id,index)"
            v-b-modal.getLagerFreeProduct
            type="success"
            round
          >
            <i class="fas fa-plus"></i> Artikel Hinzufgen</el-button
          >
        </div> 
        <div

           style="cursor:pointer;"
          @click="getActiveUser.isLagerZuruck ? showChangeStellplatzModal(data,lager,indexData,index):''"
       
          class="ware-list p-3 my-2 border-bottom"
          v-for="(data, indexData) in lager.data"
          :key="indexData"
        >
          <div class="opinion-header h5 ">
            <span style="font-weight:800"
              >Artikelnummer :    <span style="font-weight:300" class="ml-3">{{ data.artikelNum }}</span></span
            >
             <span class="ml-3" style="font-weight:800"
              >Artikel : <span style="font-weight:300" >{{ data.name }}</span></span>
          </div>
          <div>
            
          </div>
        </div></el-collapse-item>
</el-collapse>
<div  v-else style="font-weight:800">
  <el-card shadow="always"  class="my-2">
    <el-row style="font-size:20px">
      <el-col :xs="12" :sm="12" :md="6">Artikel Nummer</el-col>
      <el-col :xs="12" :sm="12" :md="6">Artikel</el-col>
      <el-col :xs="12" :sm="12" :md="6">Gang</el-col>
      <el-col :xs="12" :sm="12" :md="6">Stellplatz</el-col>
    </el-row>
    </el-card>
 <el-card class="my-1 rowHover p-0" shadow="always" v-for="(product,index) in searchProductInWarehouseItems" :key="index">
 <div @click="swipeUp(product.warehouseId)" class="p-3" style="cursor:pointer" >
      <el-row>
      <el-col :xs="12" :sm="12" :md="6">Artikel Num : {{product.artikelNum}}</el-col>
      <el-col :xs="12" :sm="12" :md="6">Artikel : {{product.pname}}</el-col>
      <el-col :xs="12" :sm="12" :md="6">Gang : {{product.gang}}</el-col>
      <el-col :xs="12" :sm="12" :md="6">Stellplatz : {{product.stellplatz}}</el-col>
    </el-row>
 </div>
    </el-card>
</div>
  </div>
  <div v-if="getLagers.length!=0">
   <div class="text-center" v-if="getLagers[getLagers.length-1].stellplatz!=1">
        <el-button
            :disabled="searching"
            class="my-2"
            @click="loadMoreStellplatz()"
            type="primary"
            round
          >
            <i class="fas fa-plus"></i> Mehr laden</el-button
          >
    </div>
  </div>
  
    <b-modal v-if="changeStellplatz.length!=0 && getLagers.length!=0" id="changeStellplatz" ref="changeStellplatz" hide-footer title="Stellplatz">
    <h4> 
       <strong> ArtikelNummer :  {{changeStellplatz.artikelNum}} </strong></h4>
      <h4>
        <strong> Artikel :  {{changeStellplatz.name}}</strong>
      </h4>
   <div>   <strong> Stellplatz : <input type="number" v-model.number="changeStellplatz.stellplatz"> </strong></div>
   <div class="text-right">
     <el-button :disabled="!usableStellplatz" @click="changeStellplatzMethods()" round :type="usableStellplatz?'primary':'danger'"> Speichern </el-button>
   </div>
    </b-modal>

    <new-lager-product-modal
      :branchName="branchName"
      :branchId="branchId"
      :index="index"
    ></new-lager-product-modal>
    <get-lager-free-product-modal
      :branchId="branchId"
      :lagerId="lagerId"
      :index="index"
    ></get-lager-free-product-modal>
  </div>
</template>

<script>
import { putApi, getApi, postApiNa} from '../../api/api'
import { mapGetters } from "vuex";
import newLagerProductModal from "./addNewLagerProductModal/addNewLagerProductModal";
import getLagerFreeProductModal from "./getLagerFreeProductModal/getLagerFreeProductModal";
export default {
  components: {
    newLagerProductModal,
    getLagerFreeProductModal
  },
  props: ["getBranchLager", "branchName", "branchId"],
  computed: {
    ...mapGetters(["getwareHouseProduct","getProducts", "getLagers","getActiveUser","getQuantityProductInLagerForBranch","getProductsBelegNum"])
  },
  watch:{
    searchProductInWarehouse(data){
      if(data==""){ this.searchProductInWarehouse =""}
      if(data.length>3){
        postApiNa('ghs/productsPlaceHelper',{ companyId : this.branchId, search : data }).then(result=>{
          // console.log(result);
            if(result.status=='success'){
              this.searchProductInWarehouseItems = result.data
               this.searching = true
            }
            else {
              this.searchProductInWarehouse = ""   
               this.searching = false
              this.searchProductInWarehouseItems=[]
                this.$notify({
          title: "Error",
          message: "Das Produkt wurde nicht gefunden.",
          type: "error",
            duration: 7000
        });
            }
          
        })
      }
    },
    'changeStellplatz.stellplatz'(data){
     if(data!=""){
        getApi('warehouse/stellplatz/'+data+'?filter=companyId:'+this.branchId).then(result=>{
        if(result.count!=0){
          this.usableStellplatz = true
          this.warehouseId = result.body[0].id
          console.log(this.warehouseId);
          
        }
        else{
          this.usableStellplatz = false
        }
      })
     }
      
    },
    getwareHouseProduct(data,oldData){
           if(this.selectedData.length!=0 && data.length!=0){
          for (let i = 0; i < this.selectedData.length; i++) {
            if(this.selectedData[i].oldId==this.getwareHouseProduct[0][0].warehouseId && !this.selectedData[i].isValid && this.activeNames!=""){
            this.selectedData.splice(i,1)
          }
          if(this.selectedData[i].oldId!=this.getwareHouseProduct[0][0].warehouseId){
            this.selectedData[i].isValid=false
            
          }
        
        }
        for (let i = 1; i < this.activeName.length; i++) {
          if(this.activeName[i]==this.activeName[i-1]){
            this.selectedData=[]
        }
            this.activeName.splice(0,1)
          }
          
        
        }

      
    }
  },
  data() {
    return {
      percentage: 10,
      searchProductInWarehouseItems:[],
      activeName:[],

      warehouseId:null,
      colors: [
        { color: "#28d43f", percentage: 20 },
        { color: "#84d428", percentage: 40 },
        { color: "#d4b428", percentage: 60 },
        { color: "#ff5e00", percentage: 80 },
        { color: "#d44a28", percentage: 100 }
      ],
      index: null,
      lagerId:null,
      lagerIndex:null,
      over:false,
      loading:true,
      selectedData: [],
      limit : 30,
      which:null,
      draggingData:[],
      activeNames:[],
      usableStellplatz:false,
      pulse:false,
      dragStartedItems:[],
      searchProductInWarehouse:"",
      miniIndex:null,
      wareId:null,

      changeStellplatz:[],
      searching:false
    };
  },
  methods: {
    swipeUp(id){
      // console.log(id);
      
       this.searching = false
      setTimeout(() => {
        var elmnt = document.getElementById(id);
        elmnt.classList.add('alert-success')
        let alert = setInterval(() => {
           elmnt.classList.add('alert-success')
           setTimeout(() => {
             elmnt.classList.remove('alert-success')
           }, 250);
        }, 500);
        setTimeout(() => {
          clearInterval(alert)
        }, 5000);
elmnt.scrollIntoView();
      }, 500);
    },
    changeStellplatzMethods(){
       console.log(this.changeStellplatz,this.warehouseId);
      putApi('warehouseProducts',this.changeStellplatz.wareId,{ warehouseId : this.warehouseId }).
      then(result=>{
         console.log(result);
        if(result.result==1){

            this.$bvModal.hide('changeStellplatz')
            let changeData =  this.$store.state.lager.lagers[this.changeStellplatz.lagerIndex].data[this.changeStellplatz.indexData]
            this.$store.state.lager.lagers[this.changeStellplatz.lagerIndex].data.splice(this.changeStellplatz.indexData,1)
      
      let pos = this.$store.state.lager.lagers.map(function(e) { return e.id; }).indexOf(this.warehouseId);
          this.$store.state.lager.lagers[pos].data.push(changeData)
      
            this.$notify({
          title: "Success",
          message: "Regal erfolgreich hinzugefügt",
          type: "success",
              duration: 7000
        });
        }
      })
      
      
    },
    showChangeStellplatzModal(row,lager,indexData,lagerIndex){
      this.changeStellplatz = {...row,...lager,indexData:indexData,lagerIndex : lagerIndex}
      this.$bvModal.show('changeStellplatz')
    },
    loadMoreStellplatz(){
        this.limit = this.limit+20
          let req = { limit : this.limit , branchId : this.branchId }
    this.$store.dispatch('initWareHouse',req)
    },
    newStellplatz(){
      this.$confirm('Möchten Sie eine Regalnummer hinzufügen?', 'Warning', {
          confirmButtonText: 'Ja',
          cancelButtonText: 'Nein',
          type: 'warning'
        }).then(() => {

         this.$store.dispatch('sendNewStellplatz',{ companyId : this.branchId }).then(result=>{
        if(result=='success'){
                this.$notify({
          title: "Success",
          message: "Regal erfolgreich hinzugefügt",
          type: "success",
              duration: 7000
        });
        }
     
          this.$message({
            type: 'success',
            message: 'erfolgreich'
          });

       })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Storniert'
          });          
        });
     
    },
    getLagerItems(id){
 
  
            // console.log(id);
            


     
      
    },
    getLagerIndex(lagerId,index) {
      // // console.log(index);
      
      this.lagerId = lagerId;
      this.index = index;
    }
  },
  mounted(){
// // console.log('MOUNTEDDDDD',this.branchId);
  let req = { limit : this.limit , branchId : this.branchId }
  // console.log('MOUNTED',this.branchId,this.limit);
    this.activeNames=[]
    this.$store.dispatch('initWareHouse',req).then(result=>{
      console.log(result);
      if(result=='success'){
        this.getLagers.forEach(lager=>{
          this.activeNames.push(lager.id)
        })
        this.loading=false
      }
      
    })
  }
};
</script>

