<template>
  <div>
 <div >
   <el-row>
       <el-col v-if="false" class="my-3 text-right" :xs="24" :sm="24" :md="23">
        <el-button type="primary" v-b-modal.newPackageDescription round>Paketbeschreibung Neu</el-button>
      </el-col>
    </el-row>
    
    <el-table
      class="border shadow p-3"
      :data="getTarifeTable"
      :default-sort="{prop: 'date', order: 'descending'}"
      style="width: 100%"
    >
      <el-table-column prop="department" label="Abteilung" sortable width="200px">
        <template slot-scope="scope">
          {{scope.row.isWhite?'Weißware':'Brauneware'}}
        </template>
      </el-table-column>
      <el-table-column prop="tarif" label="Tarif" width="200px">
        <template slot-scope="scope">
       {{scope.row.name}}
        </template>
      </el-table-column>
      <el-table-column v-if="edited" prop="description" label="Beschreibung" width="1400px"></el-table-column>
      <el-table-column prop="edit" label="Bearbeiten">
        <template slot-scope="scope">
          <span  v-b-modal.editDescription @click="editDesc(scope.row,scope.$index)">
            <i class="el-icon-edit bg-warning text-dark p-2 rounded-circle" style="font-size:18px"></i>
          </span>
            <span v-if="false"  @click="deleteDesc(scope.row.id)">
            <i class="el-icon-close bg-danger text-white p-2  rounded-circle" style="font-size:18px"></i>
          </span>
        </template>
      </el-table-column>
    </el-table>
        <b-modal
      id="newPackageDescription"
      ref="newPackageDescription"
      size="xl"
      title="Extra Large Modal"
      hide-footer
    >
      <el-row>

        <el-col :span="12" class="text-center">
          <strong style="font-weight:700">Abteilung :  </strong>
          <el-select id="label3"   @change="isitWhite" v-model="newDesc.isWhite" placeholder="Select">
            <el-option
              v-for="item in optionsPackage"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-col>
          <el-col :span="12" class="text-center">
          <strong style="font-weight:700">Tarif : </strong>
          <el-select :disabled="newDesc.isWhite==null" id="label1" v-model="newDesc.parentId" placeholder="Select">
            <el-option
              v-for="item in tarifeTable"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
        <el-col :span="22" :offset="1" class="shadow mt-5 border" >
          <textarea  v-model="newDesc.description" name="" class="form-control p-3" id="label2" cols="30" rows="10"></textarea>
        </el-col>
                <el-col :span="22" :offset="1" class="text-right mt-5" >
                  <button @click="sendNewPackageDescription()" class="btn btn-outline-primary">Speichern</button>
        </el-col>
      </el-row>
    </b-modal>
    <b-modal
      id="editDescription"
      ref="editDescription"
      size="xl"
      title="Extra Large Modal"
      hide-footer
    >
      <el-row>
        <el-col :span="12" class="text-center">
          <strong style="font-weight:700">Abteilung : </strong>
          {{ selectedEditDesc.isWhite?'Weißware':'Brauneware'}}
        </el-col>
        <el-col :span="12" class="text-center">

          <strong style="font-weight:700">Tarif : </strong>
            {{selectedEditDesc.name}}
        </el-col>

        <el-col :span="22" :offset="1" class="shadow mt-5 border" >
          <textarea id="label6"  v-model="selectedEditDesc.description" name="" class="form-control p-3" cols="30" rows="10"></textarea>
        </el-col>

                <el-col :span="22" :offset="1" class="text-right mt-5" >
                  <button @click="sendEditPackageDescription()" class="btn btn-outline-primary">Speichern</button>
        </el-col>
      </el-row>
    </b-modal>
 </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import auth from '../mixins/auth';
import isAdmin from '../mixins/isGod';
import { deleteApi } from '../api/api';
export default {
  data() {
    return {
      errPoint:[],
      loading:true,
      newDesc:{
        parentId : "",
        description: "",
        isWhite : null,

      },
      optionsType: [
        { value: 1, label: "Standart" },
        { value: 2, label: "Komfort" },
        { value: 3, label: "Premium" },
        { value: 4, label: "Techn. Hilfe" }
      ],
         optionsType2: [
        { value: 5, label: "Standart" },
        { value: 6, label: "Komfort" },
        { value: 7, label: "Premium" },
        { value: 8, label: "Techn. Hilfe" }
      ],
      optionsPackage:[
         { value: true, label: "Weißware" },
         { value: false, label: "Brauneware" },
      ],
      selectedEditDesc: [],
      tarifeTable:[],
      type:[],
      index:null,
      edited:true
    };
  },
  mixins:[auth,isAdmin],
  computed:{
    ...mapGetters(['getPackageDescriptionTable','getTarifeTable'])
  },
  methods: {
    deleteDesc(id){
              this.$confirm('This will permanently delete the file. Continue?', 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'warning'
        }).then(() => {
            deleteApi('setupDefinition',id).then(result=>{
              // // console.log(result);
              this.$message({
            type: 'success',
            message: 'Delete completed'
          });
            })
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Delete canceled'
          });          
        });
      
    },
    isitWhite(data){
      this.tarifeTable=[]
      // // console.log(data);
        this.getTarifeTable.forEach(response=>{
        if(data==response.isWhite){
          this.tarifeTable.push(response)
        }
      })
    },
    editDesc(row,index) {
      // // console.log(row);
      this.tarifeTable =[]
      this.getTarifeTable.forEach(response=>{
        if(row.isWhite==response.isWhite){
          this.tarifeTable.push(response)
        }
      })
      let department = ""
      let tarif =""
      if(row.isWhite){
        department = "Weißware"
        if(row.parentId==1){ tarif = "Standart" }
        if(row.parentId==2){ tarif = "Komfort" }
        if(row.parentId==3){ tarif = "Premium" }
        if(row.parentId==4){ tarif = "Tech. Hilfe" }
      }
      else{
        department = "Brauneware"
         if(row.parentId==5){ tarif = "Standart" }
        if(row.parentId==6){ tarif = "Komfort" }
        if(row.parentId==7){ tarif = "Premium" }
        if(row.parentId==8){ tarif = "Tech. Hilfe" }
      }
      
      this.selectedEditDesc = {...row,tarif:tarif,department:department}
      this.index = index
    },
    sendEditPackageDescription(){
      // // console.log(this.selectedEditDesc);
      
        for (let i = 0; i < this.errPoint.length; i++) {
         $(this.errPoint[i].label).removeClass("borderError")
    }

      this.errPoint=[]
      if(this.selectedEditDesc.id==""){
        this.errPoint.push({err:"ungültig Tarif", "label": "#label4"})
      }
      if(this.selectedEditDesc.description==""){
         // // console.log('HATAAA');
        this.errPoint.push({err: "ungültig Beschreibung", "label": "#label5"})
      }
      if(this.selectedEditDesc.isWhite==null){
        this.errPoint.push({err: "ungültig Abteilung", "label": "#label6"})
      }
      if(this.errPoint.length==0){
        this.$store.dispatch('sendEditPackageDescription',this.selectedEditDesc)
        .then(result=>{
          if(result=='success'){
            // console.log(    this.$store.state.branch.tarifeTable[this.index]);
            
           this.$store.state.branch.tarifeTable[this.index] = this.selectedEditDesc
           this.edited=false
           setTimeout(() => {
             this.edited=true
           }, 500);
          this.$notify({
          title: 'Erfolg',
          message: 'Die Paketbeschreibung wurde erfolgreich aktualisiert.',
          type: 'success'
        });
          this.$refs.editDescription.hide();
          }
        })
        
      }
      else {
       
        // // console.log(this.errPoint);
        
        for (let i = 0; i < this.errPoint.length; i++) {
    setTimeout(() => {
            this.$notify({
          title: "Error",
          message: this.errPoint[i].err,
          type: "error"
        });
        }, i*50);
    $(this.errPoint[i].label).addClass("borderError")
  //  $("p").css("background-color");
  }
      }
    },
    sendNewPackageDescription(){
     for (let i = 0; i < this.errPoint.length; i++) {
         $(this.errPoint[i].label).removeClass("borderError")
    }

      this.errPoint=[]
      if(this.newDesc.id==""){
        this.errPoint.push({err:"ungültig Tarif", "label": "#label1"})
      }
      if(this.newDesc.description==""){
        this.errPoint.push({err: "ungültig Beschreibung", "label": "#label2"})
      }
      if(this.newDesc.isWhite==""){
        this.errPoint.push({err: "ungültig Abteilung", "label": "#label3"})
      }
      if(this.errPoint.length==0){
     this.$store.dispatch('pushPackageDescriptionTable',this.newDesc).then(response=>{
        if(response == 'success')
        {
            this.$notify({
          title: 'Erfolg',
          message: 'Die Paketbeschreibung wurde erfolgreich aktualisiert.',
          type: 'success'
        });
          this.$refs.newPackageDescription.hide();
        }
      })
      }
      else {
        for (let i = 0; i < this.errPoint.length; i++) {
    setTimeout(() => {
            this.$notify({
          title: "Error",
          message: this.errPoint[i].err,
          type: "error"
        });
        }, i*50);
    $(this.errPoint[i].label).addClass("borderError")
  //  $("p").css("background-color");
  }
      }
       
    }
  },
};
</script>

<style>
  .borderError{
  border-left:10px solid red !important;
}
</style>