<b-modal id="showProductsInBeleg" hide-footer ref="showProductsInBeleg" size="xl">

  <div class="text-center h2" style="font-weight:700">Beleg Num : {{productInBelegData.belegNo}}</div>
  <div v-if="productInBelegData.count!=0" class="my-2">
   <div class="my-3">
         <el-button @click="backToProductHistory()" type="primary" round> <i class="el-icon-back"></i> Back</el-button>
   </div>
    <span v-if="productInBelegData.count!=0" style="font-weight:900" class="text-muted"><span style="font-size:20px">Menge : {{productInBelegData.count}}  ( {{editGoods.name}} )</span></span>
  </div>
  <div v-if="productInBelegData.count!=0" class="text-right">
    <span class="p-2 text-white mr-4" :class="productInBelegData.isAus?'bg-danger':'bg-success'">{{productInBelegData.isAus?'AUS':'EIN'}}</span>
  </div>

     <el-table
      class="border shadow p-3"
