      :default-sort="{ prop: 'date', order: 'descending' }"
      style="width: 100%"
    >
      <el-table-column prop="artikelNum" label="Artikel Num."  sortable> 
      </el-table-column>
      <el-table-column prop="name" width="400px" label="Artikel"> 
        <template slot-scope="scope">
          <div :class="scope.row.name==editGoods.name?'bg-danger text-white p-2':''">{{scope.row.name}}</div>
        </template>
      </el-table-column>
      <!-- <el-table-column prop="quantity" label="Menge"> </el-table-column> -->
      
      <el-table-column prop="gang" label="Gang">
          <template slot-scope="scope">
      <span style="font-weight:700">  Gang {{scope.row.gang}}</span>
      </template>
      </el-table-column>
      <el-table-column
        prop="stellplatz"
        label="Stellplatz"
      >
      <template slot-scope="scope">
        <span  style="font-weight:700">  Stellplatz {{scope.row.stellplatz}}</span>
       
      </template>
      </el-table-column>
           </el-table> 
</b-modal>
<el-drawer
  title="I am the title"
  :visible.sync="drawer2"
  :direction="'ttb'"
  :size="'70%'"
  :with-header="false">
  <div v-if="!settings">
<el-row>
    <el-col v-if="getActiveUser.isLagerArtikel" class="text-center  mt-5 mb-2">
        <el-button v-b-modal.newArtikel @click="drawer2=false"  type="success" class="ml-5" round> <i class="fas fa-plus"></i> Neuer Artikel</el-button>
    </el-col>
  </el-row>
  <el-row>
    <el-col :xs="1" :sm="1" :md="8">-</el-col>
    <el-col :xs="23" :sm="23" :md="8">
      <div class="border rounded shadow form-group"  style="overflow-y:scroll; overflow-x:hidden; height:500px;">
        <input type="text" name="" class="form-control p-4" v-model="searchBoxInGoods" placeholder="Artikel Nummer oder Artikel Suchen" id="">
        <el-card  shadow="always" v-for="(product,index) in getGoods" :key="index" >
          <div style="cursor:pointer;" @click="edit(product)">
          <el-row >
            <el-col :span="10">{{product.artikelNum}}</el-col>
            <el-col :span="10">{{product.name}}</el-col>
          </el-row>
          </div>

        </el-card>
      </div>
    </el-col>
  </el-row>
  </div>
  <div v-if="settings" >
    <el-row class="">
      <el-col class="text-left ml-5 mt-4">
        <el-button @click="settings=false;QauntityDetail=false"  type="primary" class="ml-5" round> <i class="fas fa-arrow-left"></i> Artikel Suchen</el-button>
      </el-col>
      <el-col  :xs="24" :sm="24" :md="QauntityDetail? 6:12" class="p-5 border shadow mt-5">
          <div class="eralp-form-control p-4">
            <div class="eralp-input-container">
              <input v-model="editGoods.artikelNum" type="text" required />
              <label>Artikel Nr.</label>
            </div>
          </div>
                  <div class="eralp-form-control  p-4">
            <div class="eralp-input-container">
              <input v-model="editGoods.name" type="text" required />
              <label>Artikel</label>
            </div>
          </div>
          <div class="text-right">
        <el-button @click="sendPutProduct"  type="success" class="ml-5" round>Speichern</el-button>

          </div>
      </el-col>
      <el-col :xs="24" :sm="24" :md="QauntityDetail? 18:12" class="p-5" >
           <el-table
           v-if="!QauntityDetail"
      class="border shadow p-3"
      :data="getProductInWarehouseForBranch"
      style="width: 100%;overflow-y:scroll; overflow-x:hidden; height:400px;"
    >
      <el-table-column prop="Cname" label="Filiale" sortable>
        <template slot-scope="scope">{{scope.row.Cname}}</template> </el-table-column>
      <el-table-column prop="adet" label="Menge"> 
        <template slot-scope="scope">
          <span @click="sendCompanyHistory(scope.row.Cname,scope.row.artikelNum)" class="text-primary" style="cursor:pointer;">{{scope.row['COUNT(p.id)']}}</span>
        </template>
      </el-table-column>
           </el-table>
        <div   v-if="QauntityDetail">
          <p>{{totalHistoryEinInCompany}} EIN - {{totalHistoryAusInCompany}} AUS = {{totalHistoryEinInCompany-totalHistoryAusInCompany}}</p>
                 <el-table
           v-if="QauntityDetail"
      class="border shadow p-3"
      :data="getPrdoductHistoryInCompany"
      style="width: 100%;overflow-y:scroll; overflow-x:hidden; height:400px;"
    >
      <el-table-column prop="belegNumber" label="Beleg Nr." width="480px" sortable> 
        <template slot-scope="scope">
          <div v-b-modal.showProductsInBeleg @click="showProductsInBeleg(scope.row,'Quantity')">
            <span class="text-danger" style="cursor:pointer" >{{scope.row.belegNumber}}</span>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="createdAt" label="Datum">
         <template slot-scope="scope">{{scope.row.createdAt.substr(0,10)}}</template>
         </el-table-column>
      <el-table-column prop="companyId" label="Filiale">
        <template>{{compName}}</template> </el-table-column>
              <el-table-column prop="menge" label="Menge">
        <template slot-scope="scope">{{scope.row['COUNT(document.belegNumber)']}}</template> </el-table-column>
      <el-table-column
        prop="isProblem"
        label="Ein / Aus"
      >
      <template slot-scope="scope">
       <span class="p-3 text-white" :class="scope.row.isAus? 'bg-danger':'bg-success'">{{scope.row.isAus ? 'AUS': 'EIN'}}</span>
      </template>
      </el-table-column>
           </el-table>
        </div>
    
                 <el-col v-if="QauntityDetail" class="text-left  mt-4">
        <el-button @click="QauntityDetail=false"  type="primary" round> <i class="fas fa-arrow-left"></i> Zurück zum Produkt</el-button>
      </el-col>
      </el-col>
    </el-row>
  </div>
  

</el-drawer>
 
 <b-modal hide-footer id="newArtikel" ref="newArtikel" size="md" title="Neuer Artikel">

    <eralp-form id="label1" @inputVModel="artikelNum = $event">Artikel Nr.</eralp-form>
    <eralp-form id="label2" @inputVModel="productName = $event">Artikel</eralp-form>
    <div class="mt-2 text-right">
        <el-button @click="sendNewProduct()"  type="success" class="ml-5" round> <i class="fas fa-plus"></i> Speichern</el-button>
    </div>
 </b-modal>

  <export-pdf :pdfData="selectedPdfData" :companyName="branchName" :companyId="branchId"></export-pdf>
  </div>
</template>

<script>
// Increments the delay on each item.


import auth from '../mixins/auth'
import isAdmin from '../mixins/isGod'
import branchOverview from '../components/LagerLayouts/branchOverview'
import wausBranchOverview from '../components/LagerLayouts/wausBranchOverview'
import exportPdf from '../components/exportPdf/exportPdfOverViewLager'
import { mapGetters } from 'vuex'
import eralpForm from '../components/eralpComponents/eralp-form'
import { getApi, getApiHelper, postApiNa } from '../api/api'
export default {
mixins:[auth],
computed:{
    ...mapGetters(['getBranch','getLagers','getProductsBelegNum','getGoods','getActiveUser','getNausgang']),
    filtredBelegNum(){
      return this.getProductsBelegNum.filter(response=>{
         return String(response.belegNumber).match(String(this.searchBox))
      })
    },

},
components:{
    branchOverview,
    wausBranchOverview,
    eralpForm,
    exportPdf
},
watch:{
  drawer(){
    if(this.drawer){
       this.$store.dispatch('sendDocumentTable',this.branchId)
       
    }
  },
  historyCompanyName(){
   this.compName = this.historyCompanyName
  },
  settings(){
    if(this.settings){
      postApiNa('ghs/depoProductHelper',{ productId: this.editGoods.id}).then(result=>{
        this.getProductInWarehouseForBranch = result.data
          
      })
    }
  },
  show(){
    if(!this.show){

      this.$store.dispatch('sendDocumentTable',this.branchId)
    }
  },

  getNausgang(){
  this.componentName = this.getNausgang

  },
  searchBoxInGoods(data){
     // // // console.log(data);
     
if(data.length>3){
      this.$store.dispatch("initProduct",data)
    }
    
  },
  getLagers(){
    this.setBranchId(this.branchId,this.branchName)
  }
},
data(){
    return  {
        show:true,
        getProductInWarehouseForBranch:[],
        productInBelegData:[],
        errPoint:[],
        editGoods:[],
        loading:true,
        compName:null,
        QauntityDetail:false,
        selectedPdfData:{
           branchName:"",
            data:[]
        },
        searchBox:"",
        searchBoxInGoods:null,
        branchId:null,
        getBranchLager:[],
        branchName:"",
        settings:false,
        componentName:"branchOverview",
        drawer:false,
        drawer2:false,
        artikelNum : "",
        productName:"",
        tableData:[
          {
            branchName : 'Alex',
            quantity : 12
          },
          {
            'branchName' : 'PP',
            quantity : 8
          }
        ],
        historyCompany:null,
        historyCompanyName:null,
        getPrdoductHistoryInCompany:[],
        totalHistoryEinInCompany:0,
        totalHistoryAusInCompany:0
        
    }
},
methods:{

  backToProductHistory(){
    this.$refs['showProductsInBeleg'].hide()
    this.drawer2=true
  },
  sendCompanyHistory(companyName,artikelNum){
    this.getBranch.forEach(branch=>{
      if(branch.name==companyName){
        this.historyCompany = branch.id
        this.historyCompanyName = companyName
      }
    })
    getApi('product/artikelNum/'+artikelNum).then(result=>{
      let productId;
      if(result.body!=null){
        productId = result.body[0].id
        // console.log(this.historyCompany,productId);
        
        postApiNa('ghs/productHistoryHelper', {companyId: this.historyCompany ,productId: productId}).then(res=>{
          // console.log(res);
          this.totalHistoryEinInCompany =0
          this.totalHistoryAusInCompany =0
          if(res.data!=null){
            this.getPrdoductHistoryInCompany = res.data
            this.getPrdoductHistoryInCompany.forEach(data=>{
              if(data.isAus){
                this.totalHistoryAusInCompany += data['COUNT(document.belegNumber)']
              }
              else {
                 this.totalHistoryEinInCompany += data['COUNT(document.belegNumber)']
              }
            })
            this.QauntityDetail = true
          }
        })
      }
    })
  },
  sendPutProduct(){
    this.$store.dispatch('sendPutProduct',this.editGoods).then(result=>{
      if(result=='success'){
        this.settings=false
      }
    })
  },
    getProducts(){
    this.$store.dispatch('sendGetProdcutsTable').then(result=>{
      if(result=='success'){
         this.drawer2=true
      }
    })
   
  },
  sendDocumentTable(){
  
  },
  showProductsInBeleg(row,tableType){
    // console.log(row);
    
    let id ;
    let productId ;
    let count=0
    if(tableType=='Quantity'){
      // console.log('QUANTITY',row.docId);
      
     id=row.docId
    }
    else{
      id=row.id
      this.editGoods=[]
      this.QauntityDetail=false
      this.settings = false
    }
        if(row.productId!=null){ productId=row.productId 
        
        
        } else { productId = 0}
    this.drawer = false
    this.drawer2 = false
    // console.log(row);
    
    this.$store.dispatch('sendWareHouseProductForDocument',id).then(response=>{
      response.body.forEach(res=>{
        if(this.editGoods.artikelNum==res.artikelNum){
          count++
        }
      })
      this.productInBelegData = { data:response.body,belegNo : row.belegNumber, isAus: row.isAus,qauntity:row.quantity, productId: productId, count:count}
    })
    
  },
  selectedPdfDataMethod(){
    // // console.log('clicked');
    let same =false
    let data =[]
    this.selectedPdfData={
      branchName:"",
      data:[]
    }
  
    this.getLagers.forEach((res)=>{

       if(this.branchId==res.branchId){
         // // console.log('burdayız branchIdler eşit');
         
         this.selectedPdfData.branchName = this.branchName
           // // console.log('branchNameyi Aldık',this.selectedPdfData.branchName);
         res.data.forEach((items)=>{
           // // console.log('lagersin datalarına girdik ');
       data.push(items)

    
      })  
        }
      
    })
    // // console.log('POPRLAR');
    
    for (var prop in data) {
      same = false
          if(this.selectedPdfData.data.length==0){
            this.selectedPdfData.data.push({...data[prop],menge:1})
          }
          else {
          for(var i in this.selectedPdfData.data){
            if(this.selectedPdfData.data[i].artikelNum == data[prop].artikelNum){
              this.selectedPdfData.data[i].menge ++
              same = true
            }
          }
          if(!same){
            this.selectedPdfData.data.push({...data[prop],menge:1})
          }
          }
           
        }
  },
  sendNewProduct(){
    this.errPoint=[]
  if(this.artikelNum=="")
{
  this.errPoint.push({err: 'ungültig Artikel Nummer',label: "#label1"})
}
if(this.productName==""){
  this.errPoint.push({err: 'ungültig Artikel',label: "#label2"})
}    


if(this.errPoint.length!=0){
  for (let i = 0; i < this.errPoint.length; i++) {
    setTimeout(() => {
            this.$notify({
          title: "Error",
          message: this.errPoint[i].err,
          type: "error"
        });
        }, i*50);
    
  //  $("p").css("background-color");
  }
}
else {
this.$store.dispatch('sendNewProduct', { artikelNum : this.artikelNum, name : this.productName , isActive: true}).then(result=>{
             if(result.result!=null){
                setTimeout(() => {
                this.artikelNum=""
                this.productName=""
            this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        }, 150);
        this.$refs['newArtikel'].hide();
             }
             else{
setTimeout(() => {
                this.artikelNum=""
                this.productName=""
            this.$notify({
          title: "Erfolg",
          message: "Bitte ändern Sie die Produktnummer.",
          type: "error"
        });
        }, 150);
             }
})

        
}



  },
edit(product){ 
  this.editGoods = product
  
  this.settings=true
},
   setBranchId(index,label){
        // // console.log(index,label);
               // // console.log(index,'over');
        let createLager=false
        this.getBranchLager=[]
    // // console.log(index);
    this.branchId=index
    this.getLagers.forEach(res=>{
      
          this.$store.commit('setBranchId',index)
        if(res.branchId == index){

 
            this.getBranchLager.push(res)
        }
        else{
          createLager=true
        }
    })


          this.branchName = label
          setTimeout(() => {
               this.show=false
          }, 1500);
       
    },
    setComponentName(event){
      this.componentName = event
    }
},
mounted(){
  
  this.componentName = this.getNausgang
  if(!this.getActiveUser.isLagerLogin){
    this.$router.push('/index')
  }
    $('#myList').removeClass('rolldown-list');
  setTimeout(function () {
    $('#myList').addClass('rolldown-list');
  }, 1);
//   $('.rolldown-list li').each(function () {
//   var delay = ($(this).index() / 4) + 's';
//   $(this).css({
//     webkitAnimationDelay: delay,
//     mozAnimationDelay: delay,
//     animationDelay: delay
//   });
//});
   if(!this.getActiveUser.isAdmin){ 
     this.setBranchId(this.getActiveUser.companyId, this.getActiveUser.companyName)
   }

}
}
</script>

<style scoped>
.rolldown-list {
  text-align: left;
  padding: 0;
  margin: 0;
}

.rolldown-list li {
  padding: 1em;
  margin-bottom: .125em;
  display: block;
  list-style: none;
  text-transform: uppercase;
    transition: 0.5s linear all;

}

.rolldown-list li {
  visibility: hidden;
  animation: rolldown .7s 1 ;
  transform-origin: 50% 0;
  animation-fill-mode: forwards;
  border-radius: 5px;
  
}

.rolldown-list li:nth-child(2n) {
  background-color: #fcf8e8;
  color: rgb(95, 91, 91) !important;
  font-weight: 700;
}

.rolldown-list li:nth-child(2n+1) {
  background-color: #005082;
  font-weight: 700;
  color: whitesmoke !important;


}
.borderError{
  border-left:10px solid red !important;
}
#myList {
    position: relative;
  width: 50%; 
  left: 50%;
  margin-left:-25%
  
}
@media only screen and (max-width: 600px) {
  #myList {
    position: relative;
  width: 100%; 
  left: 0;
  margin-left: 0 

  
}
}

#btnReload {
  float: right;
  color: rgb(255, 255, 255);
  background: #ccc;
  text-transform: uppercase;
  border: none;
  padding: .5em 1em;
}

#btnReload:hover {
  background: #ddd;
}

@keyframes rolldown {
  0% {
    visibility: visible;
    transform: rotateX(180deg) perspective(500px);
  }
  70% {
    visibility: visible;
    transform: rotateX(-20deg);
  }
  100% {
    visibility: visible;
    transform: rotateX(0deg);
  }
}
.hover:hover{
    background: #ecb390 !important;
