<template>
  <b-modal id="newBranch" ref="newBranch" size="lg" hide-footer title="Nue Filiale">
            <div class="mb-4">
        <strong

          v-if="errPoint.length != 0"
          style="font-weight:800"
          class="text-danger animated pulse infinite"
          >ERROR :</strong
        >
        <span v-for="(err, index) in errPoint" :key="index"
          ><span class="shadow" :style="tagRed">{{ err.err }}</span>
        </span>
      </div>
      <el-row>
          <el-col :span="23" class="text-right">
        <el-button @click="sendNewcompany()" type="primary" round> <i class="fas fa-address-card"></i>Speichern</el-button>
          </el-col>
      </el-row>
      <el-row>
          <el-col :sm="22" :offset="2" :md="10" :xs="22">
              <eralp-form @inputVModel="branchName=$event">Filiale</eralp-form>
              <eralp-form @inputVModel="time812=$event">8-12</eralp-form>
          </el-col>
          <el-col :sm="22" :xs="22" :offset="2" :md="10">
                            <eralp-form @inputVmodel="time1014=$event">10-14</eralp-form>
              <eralp-form @inputVModel="time1216=$event">12-16</eralp-form>
              <eralp-form @inputVModel="time1418=$event">14-18</eralp-form>
          </el-col>
      </el-row>
  </b-modal>
</template>

<script>
import eralpForm from '../../eralpComponents/eralp-form'
export default {
components:{
    eralpForm
},
data(){
    return{
        time812:0,
        time1014:0,
        time1216:0,
        time1418:0,
        errPoint:[],
              tagRed: {
        padding: "8px",
        background: "rgb(253, 167, 167)",
        color: "red",
        borderRadius: "20px",
        margin: "0 5px",
        fontWeight: 800
      },
        branchName:""
    }
},
methods:{
    sendNewcompany(){
      this.errPoint=[]
      if(this.branchName == "")
      {
        this.errPoint.push({ err: "Filiale" });
      }
      if(this.errPoint.length ==0){
            this.$store.dispatch('sendNewBranch',{ isActive:true, value : this.branchName, label: this.branchName , time812 : this.time812, time1014 : this.time1014 , time1216 : this.time1216, time1418 : this.time1418 })
            this.$refs['newBranch'].hide()
    }
      else {
        for (let i = 0; i < this.errPoint.length; i++) {
                    setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Ungültiger "+ this.errPoint[i].err,
          type: "error",
            duration: 7000
        });
       }, i*50);
        }
      }
    }
}
}
</script>

