import Vue from 'vue'
import Vuex from 'vuex'
import { postApi } from '../../api/api';

Vue.use(Vuex);

const state={
   permissions:[
    {
        date : '20.04.2020',
        time : '10-14',
        branch : 'Alex',
        permission : 1,
       },
       {
        date : '20.04.2020',
        time : '10-10',
        branch : 'EG',
        permission : 1,

       },
       {
        date : '20.04.2020',
        time : '10-12',
        branch : 'MZ',
        permission : 1,

       },
       {
        date : '20.04.2020',
        time : '10-12',
        branch : 'Postdam',
        permission : 1,

       },
   ]
}
const getters= {
    getPermissions(state){
        return state.permissions;
    }
   
}
const mutations= {
    setPushPermission(state,payload){
        state.permissions.unshift(payload)
    }
}

const actions= {
   pushPermissions(VuexContext,payload){
        //APİ İŞLEMLERİ 
        // console.log(payload);
     return   postApi('companyAddRight',{ companyId : payload.branch, quantity: payload.permission, atTime : payload.time , atDate : payload.date }).then(result=>{
            // console.log(result);
            VuexContext.commit('setPushPermission',{ id :result.result, companyId : payload.branch, quantity: payload.permission, atTime : payload.time , atDate : payload.date })
            return { status :'success', data : { id :result.result, companyId : payload.branch, quantity: payload.permission, atTime : payload.time , atDate : payload.date }}
        })

      
      
    }
}

export default{
    state,
    getters,
    mutations,
    actions
}