<template>
  <div>
<div v-if="loading"></div>
<div v-else>
      
    <el-popover
      v-if="filtred.length!=0 || resultCategoryFiltre.length!=0"
      placement="left"
      width="75"
      trigger="click"
    >
      <vue-excel-xlsx
        :data="filterCategory? resultCategoryFiltre:filtred"
        :columns="columns"
        :filename="'Lager Excell Data'"
        :sheetname="'Lager Excell Data'"
        class="btn btn-outline-success"
      >Download</vue-excel-xlsx>
      <i
        slot="reference"
        class="text-white shadow bg-success border rounded-circle p-3 px-4 mr-3 mb-3 position-fixed fas fa-file-excel excellIcon"
      ></i>
    </el-popover>
    <div class="excellKomplettIcon2" v-b-modal.excellExport >
      <i
        class="text-dark rounded-circle p-3 px-4 mr-3 mb-3 position-fixed fas fa-file-excel excellKomplettIcon2"
      ></i>
      <svg
        style="width:100px"
        class="text-dark text-center shadow bg-warning border rounded-circle position-fixed fas fa-file-excel excellKomplettIcon"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
        version="1.1"
        viewBox="0 20 400 400"
      >
        <defs>
          <!-- The text path: see links above regarding coordinate system -->
          <path d="M0, 200a200, 200 0 1, 0 400, 0a200, 200 0 1, 0 -400, 0" id="txt-path" />
        </defs>

        <text fill="#D54E02" font-size="65.5" font-family="Helvetica Neue" font-weight="400">
          <!-- This is the magic -->

          <textPath startOffset="50" xlink:href="#txt-path">Export Komplett</textPath>
        </text>
      </svg>
    </div>
    <el-row class="border shadow p-2">
     
      <el-col :md="7" :sm="24" :xs="24">
        <div class="w-100 bg-white p-3 mt-3 text-center">
          <span
            style="cursor:pointer;font-weight:700;border-radius:25px 0 0 25px"
            @click="lager='All'"
            class="p-2 px-md-3 border transition"
            :style="lager=='All' ? {color: '#0063b2', backgroundColor : '#9cc3d5'} :  {color: '#89abe3', backgroundColor : '#fcf6f5'}"
          >All</span>
          <span
            style="cursor:pointer;font-weight:700;"
            @click="lager='isHaus'"
            class="p-2 px-md-3 border transition"
            :style="lager=='isHaus' ? {color: '#0063b2', backgroundColor : '#9cc3d5'} :  {color: '#89abe3', backgroundColor : '#fcf6f5'}"
          >Hauslager</span>
          <span
            style="cursor:pointer;font-weight:700;"
            @click="lager='isAus'"
            class="p-2 px-md-3 border transition"
            :style="lager=='isAus' ? {color: '#0063b2', backgroundColor : '#9cc3d5'} :  {color: '#89abe3', backgroundColor : '#fcf6f5'}"
          >Außenlager</span>
          <span
            style="cursor:pointer;font-weight:700;border-radius:0 25px 25px 0"
            @click="lager='isMich'"
            class="p-2 px-md-3 border transition"
            :style="lager=='isMich' ? {color: '#0063b2', backgroundColor : '#9cc3d5'} :  {color: '#89abe3', backgroundColor : '#fcf6f5'}"
          >Mischlager</span>
        </div>
      </el-col>
      <el-col class="text-center" :offset="2" :sm="23" :xs="23" :md="4">
        <div class="form-group">
          <strong style="font-weight:700">Datum</strong>
          <el-date-picker
            class="w-100"
            v-model="datePickerElement"
            type="date"
            placeholder="Pick a day"
          ></el-date-picker>
        </div>
      </el-col>
      <el-col class="text-right mt-2" :offset="1" :sm="13" :xs="13" :md="4">
        <div class="eralp-form-control">
          <div class="eralp-input-container">
            <input v-model="searchBox" type="text" required />
            <label>{{search.label}}</label>
          </div>
        </div>
      </el-col>
      <el-col :sm="7" :xs="7" :md="3" class="my-3 ml-3">
        <el-dropdown>
          <el-button round type="primary">
            {{search.label}}
            <i class="el-icon-arrow-down el-icon--right"></i>
          </el-button>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>
              <div @click="searchingBeleg()">Beleg</div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="SearchingPlz()">Plz</div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="SearchingUser()">User</div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="SearchingVorname()">Vorname</div>
            </el-dropdown-item>
            <el-dropdown-item>
              <div @click="SearchingNachname()">Nachname</div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col v-if="getActiveUser.isAnlegen" class="my-3 text-right" :xs="24" :sm="24" :md="2">
        <el-button  type="primary" v-b-modal.modal-xl round> <i class="fas fa-address-card"></i>  Neu</el-button>
      </el-col>
    </el-row>
    <eralp-table
      :tableFiltred="filterCategory? resultCategoryFiltre:filtred"
      @data="filtredData=$event"
    ></eralp-table>
    <new-product-modal></new-product-modal>
    <excell-export :tableData="filtredData"></excell-export>
</div>
  </div>
</template>

<script>
import excellExport from "../components/Modals/ExcellModal/excellModal";
import eralpTable from "../components/Table/eralpTable";
import newProductModal from "../components/Modals/NewProduct";
import { dateFormatter, dateFormatterElement } from "../dateFormatter";
import auth from '../mixins/auth'
import { mapGetters } from 'vuex';
export default {
  components: {
    eralpTable,
    excellExport,
    newProductModal
  },
  data() {
    return {
      search: { type: "beleg", label: "Beleg" },
      searchBox: "",
      lager: "All",
      filtredData: [],
      categoryFiltred: [],
      filterCategory: false,
      loading:true,
      datePicker: false,
      datePickerElement: null,
      date: null,
      columns: [
        { label: "Zeit", field: "setupTime" },
        {
          label: "Vorname",
          field: "customerName"
        },
        {
          label: "Nachname",
          field: "customerLastname"
        },
        {
          label: "Straße",
          field: "customerAddress"
        },
        {
          label: "Telefon",
          field: "customerPhone"
        },
        {
          label: "Plz",
          field: "zipCode"
        },
        {
          label: "Beleg",
          field: "beleg"
        },
        {
          label: "Filiale",
          field: "companyId"
        },
        {
          label: "Tarif",
          field: "setupDefinition"
        },
        {
          label: "Abteilung",
          field: "department"
        }
      ]
    };
  },
  mixins:[auth],
  computed: {
    ...mapGetters(['getToday','getActiveUser',"getCustomers"]),
    filtred() {
      if (this.date != null) {
        return this.filtredData.filter(response => {
          if (this.date == response.setupDate) {
            return String(response[this.search.type])
              .toLowerCase()
              .match(this.searchBox.toLowerCase());
          }
        });
      } else {
        return this.filtredData.filter(response => {
          return String(response[this.search.type])
            .toLowerCase()
            .match(this.searchBox.toLowerCase());
        });
      }
    },
    resultCategoryFiltre() {
      if (this.date != null) { 
        return this.categoryFiltred.filter(response => {
          if (this.date == response.setupDate) {
            return String(response[this.search.type])
              .toLowerCase()
              .match(this.searchBox.toLowerCase());
          }
        });
      } else {
        return this.categoryFiltred.filter(response => {
          return String(response[this.search.type])
            .toLowerCase()
            .match(this.searchBox.toLowerCase());
        });
      }
    }
  },
  watch: {
    datePickerElement(event) {
      // // console.log(event);
      if (event == null) {
        this.searchBox = "";
        this.datePicker = false;
        this.date=null
        this.search = { type: "beleg", label: "Beleg" };
      } else {
        this.datePicker = true;
        let picker = dateFormatterElement(event);
        picker = dateFormatter(picker);
        this.date = picker;
        // this.searchBox = this.date;
        // this.search = { type: "date", label: "Datum" };
      }
    },
    lager() {
      this.categoryFiltred = [];
      this.filtredData.filter(response => {
         this.filterCategory = true;
         if(this.lager=='isHaus'){
           if (response.isHaus && !response.isAus) {
          this.categoryFiltred.push(response);
          // // console.log(this.categoryFiltred);
        }
         }
         else if(this.lager=="isAus"){
             if (response.isAus && !response.isHaus) {
          this.categoryFiltred.push(response);
          // // console.log(this.categoryFiltred);
        }
         }
         else if(this.lager=="isMich") {
           if (response.isAus && response.isHaus) {
          this.categoryFiltred.push(response);
          // // console.log(this.categoryFiltred);
        }
         }
        
      });
      if (this.lager == "All") {
        this.filterCategory = false;
      }
    }
  },
  methods: {
    searchingBeleg() {
      this.searchBox = "";
      this.datePicker = false;
      this.search = { type: "beleg", label: "Beleg" };
    },
    SearchingPlz() {
      this.searchBox = "";
      this.datePicker = false;

      this.search = { type: "zipCode", label: "Plz" };
    },
    SearchingUser() {
      this.searchBox = "";
      this.datePicker = false;

      this.search = { type: "uUsername", label: "User" };
    },

    SearchingVorname() {
      this.datePicker = false;

      this.searchBox = "";
      this.search = { type: "customerName", label: "Vorname" };
    },
    SearchingNachname() {
      this.searchBox = "";
      this.datePicker = false;

      this.search = { type: "customerLastname", label: "Nachname" };
    }
  },
  mounted(){
    if(this.getCustomers.length==0){
      this.$store.dispatch('initOrdersTable',this.getActiveUser.companyId).then(result=>{
      if(result=="success"){
        this.$store.dispatch('initSetupDefinitionTable').then(res=>{
          if(res=='success'){
            this.loading=false
          }
        })
        
      }
      else if(result=='fail'){
        localStorage.clear()
        this.$router.push('/login')
      }
    })
 }
 else{
    this.loading=false
 }
    
  }
};
</script>

