<template>
  <div class="p-3">
    <el-row class="border shadow p-2">
      <el-col :md="6" :sm="24" :xs="24">
      </el-col>
      <el-col class="text-center" :offset="1" :sm="23" :xs="23" :md="5">
        ** Helper geldiğinde çalışacak.
        <el-select v-model="selectedBranch" class="w-100 mt-4" clearable placeholder="Select">
          <el-option
            v-for="item in getBranch"
            :key="item.id"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-col>
      <el-col class="my-3 text-right" :xs="24" :sm="24" :md="16">
        <el-button type="primary" v-b-modal.newPermission round>Sonderzeiten Neu</el-button>
      </el-col>
    </el-row>
    <el-table
      class="border shadow p-3 mt-3"
      :data="filtred"
      :default-sort="{prop: 'date', order: 'descending'}"
      style="width: 100%"
    >
      <el-table-column prop="atDate" label="Datum" sortable></el-table-column>
      <el-table-column prop="atTime" label="Uhrzeit" sortable></el-table-column>
      <el-table-column prop="name" label="Filiale">
        <template slot-scope="scope">{{scope.row.cid==3?'8 Filliale':scope.row.name}}</template>
      </el-table-column>
      <el-table-column prop="quantity" label="Menge"></el-table-column>
    </el-table>
    <new-permission-modal @newPermission="newPermission=$event"></new-permission-modal>
  </div>
</template>

<script>
import { dateFormatter } from "../dateFormatter";
import newPermissionModal from "../components/Modals/newPermission/newPermissionModal";
import { mapGetters } from 'vuex';
import auth from '../mixins/auth';
import isAdmin from '../mixins/isGod';
import { getApi } from '../api/api';
export default {
  components: {
    newPermissionModal,
  },

  data() {
    return {
      lager: "All",
      editedUser: [],
      editUserIndex: null,
      editStatus: false,
      category: false,
      newPermission:[],
      selectedBranch: "",
      searchBox: "",
      search: { type: "name", label: "Vorname" },
      datePickerElement: null,
      tableData:[],
      editUserItems: [],
      filtredName: null,
      permissionData:[]
    };
  },
  mixins:[auth,isAdmin],
  computed: {
    ...mapGetters(['getBranch','getPermissions']),
    filtred() {
      if (this.lager != "All") {
        return this.permissionData.filter(response => {
          if (response[this.lager]) {
            if (this.selectedBranch == "") {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            } else {
              if (response.branch == this.selectedBranch) {
                return String(response[this.search.type])
                  .toLowerCase()
                  .match(this.searchBox.toLowerCase());
              }
            }
          }
        });
      } else {
        if (this.selectedBranch == "") {
          if (this.searchBox == "") {
            return this.permissionData;
          } else {
            return this.permissionData.filter(response => {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            });
          }
        } else {
          return this.permissionData.filter(response => {
            if (response.branch == this.selectedBranch) {
              return String(response[this.search.type])
                .toLowerCase()
                .match(this.searchBox.toLowerCase());
            }
          });
        }
      }
    },
    filtredBranch() {
      if (this.lager != "All") {
        return this.permissionData.filter(response => {
          if (response[this.lager]) {
            if (this.selectedBranch == "") {
              return response;
            } else {
              if (response.branch == this.selectedBranch) {
                return response;
              }
            }
          }
        });
      } else {
        if (this.selectedBranch == "") {
          return this.permissionData;
        } else {
          return this.permissionData.filter(response => {
            if (response.branch == this.selectedBranch) {
              return response;
            }
          });
        }
      }
    }
  },
  methods: {
    searchingUser(type) {
      if (type == "login") this.search = { type: "login", label: "Login" };
      if (type == "name") this.search = { type: "name", label: "Vorname" };
      if (type == "lastname")
        this.search = { type: "lastname", label: "Nachname" };
    },
    editUser(index) {
      //  let num = this.tableData.length
      //   num = num-1
      this.editUserIndex = index;
      this.editUserItems = this.permissionData[index];
    }
  },

  watch: {
    newPermission(data){
      this.permissionData.unshift(data)
    },
    editedUser(){
      // console.log(this.editedUser,'worked');
      
      this.permissionData[this.editUserIndex] = this.editedUser
    },
    selectedBranch() {
      // console.log(this.selectedBranch);
    }
  },
  mounted(){
    getApi('companyAddRight').then(result=>{
      // console.log(result);
      
      result.body.forEach(body=>{
         this.getBranch.forEach(branch=>{
           if(body.companyId==branch.id){
             this.permissionData.push({...body,cid:branch.id, name : branch.name})
           }
         })
      })
    })
  }
};
</script>
