<template>
  <div class="p-5">
    <el-row>
      <el-col
        :sm="24"
        :xs="24"
        :md="24"
        class="border shadow rounded p-3"
      >
      <el-row>
        <el-col   class="text-center" :md="12" :sm="24" :xs="24">
                 <span style="font-weight:800">Filiale : </span> <br>
      <el-select :disabled="!getActiveUser.isAdmin" v-model="selectedBranch" placeholder="Select">
              <el-option
                v-for="item in getBranch"
