                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-col>
        <el-col class="text-center" :md="12" :lg="6" :sm="24" :xs="24">
          <span  style="font-weight:800">Datum : </span> <br>
              <el-date-picker
              class="w-100"
              v-model="selectedDatePicker"
              type="daterange"
              align="right"
              unlink-panels
              range-separator="To"
              start-placeholder="Start date"
              end-placeholder="End date"
              :picker-options="pickerOptions"
            >
            </el-date-picker>
          
        </el-col>
        <el-col :span="24" class="text-center mt-5">
          <el-button  @click="search()" round type="primary">Suche</el-button>
        </el-col>
      </el-row>

           
             
          
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-row v-if="showTable">
          <el-col :span="12">
            <div>
              <div style="margin-top: 20px">
                <el-button @click="toggleSelection()"
                  >Clear selection</el-button
                >
              </div>
            </div>
          </el-col>
          <el-col
            v-if="multipleSelection.length > 1"
            :span="12"
            class="text-right mt-3"
          >
            <el-button
              @click="drawer = true"
              type="primary"
              style="margin-left: 16px;"
            >
              Change to Status
            </el-button>
          </el-col>
        </el-row>
        <el-table
          ref="multipleTable"
          :row-class-name="tableRowClassName"
          v-if="showTable"
          class="border shadow p-3 mt-3"
          :data="filtred"
          :default-sort="{ prop: 'date', order: 'descending' }"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          >
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column
            prop="beleg"
            label="Beleg"
            sortable
          ></el-table-column>
          <el-table-column prop="STATUS" label="Status" sortable>
            <template slot-scope="scope">
              <el-popover placement="top" width="250" trigger="click">
                <el-select
                  @change="editedStatus()"
                  v-model="editSelectedStatus"
                  placeholder="Select"
                >
                  <el-option
                    v-for="item in getStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
                <p
                  slot="reference"
                  @click="editStatus(scope.row,scope.row.orderid,scope.$index)"
                  class="text-primary"
                  style="cursor:pointer"
                >
                  {{
                    scope.row.STATUS == "handover"
                      ? "Übergeben"
                      : scope.row.STATUS == "Nhandover"
                      ? "Nicht Übergeben"
                      : scope.row.STATUS == "provided"
                      ? "Bereitgestellt"
                      : scope.row.STATUS == "Nprovided"
                      ? "Nicht Bereitgestellt"
                      : scope.row.STATUS == "transmitted"
                      ? "Übermittelt"
                      : scope.row.STATUS == "Nsubmitted"
                      ? "Nicht Übermittelt"
                      : ""
                  }}
                </p>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column prop="customerName"  label="Vorname"></el-table-column>
          <el-table-column prop="customerLastname" label="Nachname"></el-table-column>
          <el-table-column prop="zipCode" label="Plz"></el-table-column>
          <el-table-column prop="customerAddress" label="Straße"></el-table-column>
          <el-table-column prop="setupTime" label="Uhrzeit"></el-table-column>
          <el-table-column prop="setupDate" label="Datum"></el-table-column>
          <el-table-column prop="lager" sortable label="Lager">
            <template slot-scope="scope">
              {{scope.row.isAus&&scope.row.isHaus?'Michlager':scope.row.isAus?'Außenlager':'Hauslager'}}
            </template>
          </el-table-column> </el-table>
      </el-col>
    </el-row>
    <el-drawer
      ref="multiDrawer"
      size="70%"
      title="Change to Status"
      :visible.sync="drawer"
      :direction="direction"
    >
      <div class="p-3">
        <el-row>
          <el-col :span="23" class="text-right">
            <el-select
              @change="editedMultiStatus()"
              v-model="editSelectedMultiStatus"
              placeholder="Select"
            >
              <el-option
                v-for="item in getStatus"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
            <el-button
              @click="editedMultiStatus()"
              class="mx-2"
              type="primary"
              round
            >
              <i class="fa fa-check "></i> Suchen</el-button
            >
          </el-col>
        </el-row>
        <h3 style="font-weight:700;text-decoration:underline">
          Deine Entscheidungen
        </h3>
  <div style="overflow-y:scroll; overflow-x:hidden; height:400px;">
            <el-table
          :row-class-name="tableRowClassName"
          v-if="showTable"
          class="border shadow p-3 mt-3"
          :data="multipleSelection"
          :default-sort="{ prop: 'date', order: 'descending' }"
          style="width: 100%"
        >
          <el-table-column type="selection" width="55"> </el-table-column>
          <el-table-column
            prop="beleg"
            label="Beleg"
            sortable
          ></el-table-column>
          <el-table-column prop="STATUS" label="Status" sortable>
            <template slot-scope="scope">
              <el-popover placement="top" width="250" trigger="click">
                <el-select
                  @change="editedStatus()"
                  v-model="editSelectedStatus"
                  placeholder="Select"
                >
                  <el-option
                    v-for="item in getStatus"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value"
                  >
                  </el-option>
                </el-select>
                <p
                  class="bg-success shadow rounded p-2 text-white"
                  slot="reference"
                  @click="editStatus(scope.$index)"
                  style="cursor:pointer"
                >
                  {{
                    scope.row.STATUS == "handover"
                      ? "Übergeben"
                      : scope.row.STATUS == "Nhandover"
                      ? "Nicht Übergeben"
                      : scope.row.STATUS == "provided"
                      ? "Bereitgestellt"
                      : scope.row.STATUS == "Nprovided"
                      ? "Nicht Bereitgestellt"
                      : scope.row.STATUS == "transmitted"
                      ? "Übermittelt"
                      : scope.row.STATUS == "Nsubmitted"
                      ? "Nicht Übermittelt"
                      : ""
                  }}
                </p>
              </el-popover>
            </template>
          </el-table-column>
          <el-table-column prop="customerName"  label="Vorname"></el-table-column>
          <el-table-column prop="customerLastname" label="Nachname"></el-table-column>
          <el-table-column prop="zipCode" label="Plz"></el-table-column>
          <el-table-column prop="customerAddress" label="Straße"></el-table-column>
          <el-table-column prop="setupTime" label="Uhrzeit"></el-table-column>
          <el-table-column prop="setupDate" label="Datum"></el-table-column>
          <el-table-column prop="lager" sortable label="Lager">
            <template slot-scope="scope">
              {{scope.row.isAus&&scope.row.isHaus?'Michlager':scope.row.isAus?'Außenlager':'Hauslager'}}
            </template>
          </el-table-column>
        </el-table>
  </div >
      </div>
    </el-drawer>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import {putApi, postApiNa} from '../api/api'
import {
  dateFormatter,
  dateFormatterElement,
  dateFormatterConvertNum
} from "../dateFormatter";
import auth from "../mixins/auth";
import isAdmin from "../mixins/isGod";
export default {
  mixins: [auth],
  computed: {
    ...mapGetters(["getBranch", "getStatus", "getLager", "getCustomers","getActiveUser"]),
    
  },
  data() {
    return {
      selectedBranch: null,
      index: null,
      filtred:[],
      editSelectedMultiStatus: null,
      showTable: false,
      selectedDatePicker: null,
      selectedStatus: null,
      selectedLager: null,
      startDate: null,
      endDate: null,
      statusId:null,
      startDateNum: null,
      drawer: false,
      direction: "btt",
      editSelectedStatus: null,
      endDateNum: null,
      multipleSelection: [],
      pickerOptions: {
        shortcuts: [
          {
            text: "Last week",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "Last month",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            }
          },
          {
            text: "Last 3 months",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            }
          }
        ]
      }
    };
  },
  methods: {
    search(){
      postApiNa('ghs/statusHelper',{ companyId : this.selectedBranch, startDate : this.startDate , endDate: this.endDate}).then(result=>{
        this.filtred=result.data
        this.showTable = true
      })
    },
    editedMultiStatus() {
      this.multipleSelection.forEach(res => {
        // // console.log(res,this.editSelectedMultiStatus);
        putApi('orders',res.orderid,{ STATUS : this.editSelectedMultiStatus })
        res.STATUS = this.editSelectedMultiStatus;
      });
      setTimeout(() => {
        this.$refs["multiDrawer"].hide();
         this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
      }, 1000);
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    tableRowClassName({ row, rowIndex }) {

    },
    editStatus(row,id,index) {
      this.editSelectedStatus = row.STATUS
      this.index = index
      // console.log(id);
      
      this.statusId = id
    },
    editedStatus(value,id) {
      this.filtred[this.index].STATUS = this.editSelectedStatus;
      putApi('orders',this.statusId,{ status : this.editSelectedStatus })
      .then(res=>{
        // // console.log(res);
         this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        
      })
      
    }
  },
  watch: {
    selectedDatePicker(value) {
      this.startDate = dateFormatterElement(value[0]);
      this.endDate = dateFormatterElement(value[1]);
      // this.startDate = dateFormatter(startDate);
      // this.endDate = dateFormatter(endDate);
      // this.startDateNum = dateFormatterConvertNum(startDate);
      // this.endDateNum = dateFormatterConvertNum(endDate);
    }
  },
  mounted(){
    if(!this.getActiveUser.isAdmin){
      this.selectedBranch=this.getActiveUser.companyId
    }
  }
};
</script>

