export const postApiNaLogin = (dataURL,data) => {
  var myHeaders = new Headers();
  myHeaders.append("Content-Type", "application/json");
  
  var raw = JSON.stringify(data);
  
  var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
  };
  
 return fetch(API_URL_3+dataURL, requestOptions)
    .then(response => response.json())
    .then(result => {
      return result})
    .catch(error => console.log('error', error));
}

export const postApiNa = (dataURL,data) => {
  var myHeaders = new Headers();
  let token = store.state.users.tokenNa
  myHeaders.append("Content-Type", "application/json");  
  myHeaders.append("token", token);
  
  var raw = JSON.stringify(data);
  
  var requestOptions = {
    method: 'POST',
    headers: myHeaders,
    body: raw,
    redirect: 'follow'
  };
  
 return fetch(API_URL_3+dataURL, requestOptions)
    .then(response => response.json())
    .then(result => {
      return result})
    .catch(error => console.log('error', error));
}
