import Vue from 'vue'
import Vuex from 'vuex'
import { BFormInvalidFeedback } from 'bootstrap-vue';
import { getApi, putApi } from '../../api/api';

Vue.use(Vuex);

const state={
  trucks:[],
  trucksItem:[],
  resultTruck:[],
  truckOrder:[]
}
const getters= {
    getTrucks(state){
        return state.trucks;
    },
    getTrucksItem(state){
     return state.trucksItem
    },
    getTruckOrder(state){
        return state.truckOrder;
    }
}
const mutations= {
    setNewDriver(state,payload){
        state.trucks.push({id:12, name :payload})
    },
    setTruckItem(state,payload){
        // // console.log(payload);
            state.trucksItem.push(payload)
    },
    setRemoveTruck(state,payload){
        state.trucks.splice(payload.index,1)
    },
    removeTruckItem(state){
        // console.log('SİLDİM');
        
        state.trucksItem = []
        // console.log(  state.trucksItem );

    },
    setTruckTable(state,payload){
        // // console.log(payload);
        
        state.trucks = payload.body
    },
    setOrdersForTruck(state,payload){
        state.truckOrder = payload
    }
}

const actions= {
    initOrdersForTruck(VuexContext,payload){
        // // console.log(payload);
            VuexContext.state.truckOrder=[]
        return getApi('orders?filter=setupDate:"'+payload+'",isActive:1').then(result=>{
            // // console.log(result);
            VuexContext.commit("setOrdersForTruck",result.body)
            return 'success'
        })
    },
    initTruckTable(VuexContext){
       return getApi('truck').then(result=>{
            VuexContext.commit('setTruckTable',result)
            return {result:'success',body : result}
        })
    },

    sendNewDriver(VuexContext,payload){
        //api pages/truck.vue
        VuexContext.commit('setNewDriver',payload)
    },
 sendTruckItem(VuexContext,payload){
     //APIII pages/truck.vue
     let data = payload.data
     let oneMore =false
     data.forEach(order=>{
          putApi('orders',order.id, { truckId : payload.truckId}).then(result=>{
              // // console.log(result);
                VuexContext.commit('setTruckItem',payload)
          })
     })
    
   
 },
 sendTruckOrdersItem(VuexContext,payload){
   
    let dontSame = true
    if(VuexContext.state.trucksItem.length!=0){
        VuexContext.state.trucksItem.forEach(order=>{
            if(order.truckId==payload.truckId){
                order.data.push(payload.data[0])
                dontSame=false
            }
        })
        if(dontSame){
            VuexContext.state.trucksItem.push(payload)
        }
    }
    else {
        VuexContext.state.trucksItem.push(payload)
    }

  //  VuexContext.commit('setTruckItem',payload)
 },
 removeTruck(VuexContext,payload){
     //APIII   pages/trucks.vue

     // // console.log(payload);
     
     VuexContext.commit('setRemoveTruck',payload)
 }
}

export default{
    state,
    getters,
    mutations,
    actions
}