import { mapGetters } from "vuex";
import { cryptoData, decryptoData } from "../secure";

export default {
    computed:{
        ...mapGetters(['getIsAuth','getUsers','getBranch'])
    },
    data(){
        return {
            tokenId:null
        }
    },
    created(){
      //  let tokenId = JSON.parse(localStorage.getItem('tokenId'))
      if(localStorage.token_N_a){
        let  tokenNa = decryptoData(localStorage.getItem('token_N_a'))
        this.$store.state.users.tokenNa = tokenNa
    }
    if(localStorage.token_H_a){
        let tokenHa = decryptoData(localStorage.getItem('token_H_a'))
        this.$store.state.users.tokenHa = tokenHa
    }
    if(localStorage.tokenId){
        this.tokenId = decryptoData(localStorage.getItem('tokenId'))
    }
     
    },

    mounted(){
        this.tokenId = JSON.parse(this.tokenId)
        // console.log(this.tokenId,'fdaslkfjlaskalk');
        
        if(this.tokenId)
        {
            let companyName = ""
        let userId = this.tokenId.id
        let companyId = this.tokenId.companyId
            this.getUsers.filter(response=>{
                if(response.id == userId){    
                    this.getBranch.forEach(company=>{
                        if(company.id == companyId){
                            companyName = company.name
                        }
                    })
                    this.$store.commit('setActiveUser',{...response,companyName:companyName})
                }
            })
        }
        else {
        // console.log(this.tokenId);
        }
        if(localStorage.token){
            this.$store.dispatch('hasToken',true)
        }

        if(this.tokenId==null){
                this.$router.push('/login')
        }
    }
}