<template>
  <b-modal id="newUser" size="xl" title="Extra Large Modal" hide-footer>
          <div class="mb-4">
        <strong
    
          v-if="errPoint.length != 0"
          style="font-weight:800"
          class="text-danger animated pulse infinite"
          >ERROR :</strong
        >
        <span v-for="(err, index) in errPoint" :key="index"
          ><span class="shadow" :style="tagRed">{{ err.err }}</span>
        </span>
      </div>
    <el-row>
        <el-col :span="12" class="text-left">
        <el-select v-model="selectedBranch" clearable placeholder="Select">
          <el-option
            v-for="item in getBranch"
            :key="item.value"
            :label="item.name"
            :value="item.id"
          ></el-option>
        </el-select>
      </el-col>
      <el-col :span="12" class="text-right">
        <el-button @click="sendNewUser()" type="primary" round>Speichern</el-button>
      </el-col>
    </el-row>
    <el-row class="mt-3">
      <el-col :xs="24" :sm="24" :offset="2" :md="9">
        <div class="p-3 border shadow rounded">
          <h3
            class="text-center text-primary"
            style="font-weight:700; text-decoration:underline"
          >Vollständiger Name</h3>

          <div class="form-group">
            <eralp-form @inputVModel="user.name=$event">Vorname</eralp-form>
          </div>
          <div class="form-group mt-2">
            <eralp-form @inputVModel="user.lastname=$event">Nachname</eralp-form>
          </div>
        </div>
      </el-col>
      <el-col :xs="24" :sm="24" :md="9" :offset="2">
        <div class="border p-3 shadow rounded">
          <h3
            class="text-center text-primary"
            style="font-weight:700; text-decoration:underline"
          >Login</h3>

          <div class="form-group">
            <eralp-form @inputVModel="user.username=$event">Username</eralp-form>
          </div>
          <div class="form-group">
            <eralp-form @inputVModel="user.password=$event">Passwort</eralp-form>
          </div>
        </div>
      </el-col>
    </el-row>
      <hr />
    <el-row>

      <div>
        <h3
          class="text-center text-primary my-5"
          style="font-weight:700; text-decoration:underline"
        >Berechtigung Zuteilen</h3>
        <el-col :xs="24" :sm="24" :md="10" class>
          <div class="form-group">
            <el-checkbox
              :indeterminate="isIndeterminate"
              v-model="checkAll"
              @change="handleCheckAllChange"
            >Check all</el-checkbox>
            <div style="margin: 15px 0;"></div>
            <el-checkbox-group v-model="checkedAuthority" @change="handleCheckedCitiesChange">
              <el-checkbox v-for="city in authority" :label="city" :key="city">{{city}}</el-checkbox>
            </el-checkbox-group>
          </div>
        </el-col>
        <el-col class :xs="24" :sm="24" :md="10" :offset="2">
          <div class="form-group">
            <div class="form-group">
              <el-checkbox
                :indeterminate="isIndeterminateLager"
                v-model="checkAllLager"
                @change="handleCheckAllChangeLager"
              >Check all</el-checkbox>
              <div style="margin: 15px 0;"></div>
              <el-checkbox-group
                v-model="checkedAuthorityLager"
                @change="handleCheckedCitiesChangeLager"
              >
                <el-checkbox v-for="city in authorityLager" :label="city" :key="city">{{city}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </el-col>
      </div>
    </el-row>
  </b-modal>
</template>

<script>
import eralpForm from "../../eralpComponents/eralp-form";
import { mapGetters } from 'vuex';
const authorityOptions = [
  "Auftrag anlegen",
  "Auftrag stornieren",
  "Belegnr. ändern",
  "Beleg ändern"
];
const authorityOptionsLager = [
  "Lager Login",
  "Lager Schreibrechte",
  "Lager Artikelverwaltung",
  "Lager zurück auf Ablage",
  "Lager Storno Beleg"
];
export default {
  components: {
    eralpForm
  },
  data() {
    return {
            tagRed: {
        padding: "8px",
        background: "rgb(253, 167, 167)",
        color: "red",
        borderRadius: "20px",
        margin: "0 5px",
        fontWeight: 800
      },
      checkAll: false,
      checkedAuthority: [],
      authority: authorityOptions,
      isIndeterminate: true,
      checkAllLager: false,
      checkedAuthorityLager: [],
      authorityLager: authorityOptionsLager,
      isIndeterminateLager: true,
      user: {
        name: "",
        lastname: "",
        username: "",
        password: ""
      },
      options: [
        {
          value: "PP",
          label: "PP"
        },
        {
          value: "Alex",
          label: "Alex"
        },
        {
          value: "Treptow",
          label: "Treptow"
        },
        {
          value: "EG",
          label: "EG"
        },
        {
          value: "Potsdam",
          label: "Potsdam"
        },
        {
          value: "MZ",
          label: "MZ"
        },
        {
          value: "ESM",
          label: "ESM"
        },
        {
          value: "Köpenick",
          label: "Köpenick"
        },
        {
          value: "GB",
          label: "GB"
        },
        {
          value: "Steglitz",
          label: "Steglitz"
        },
        {
          value: "EPK",
          label: "EPK"
        },
        {
          value: "EPB",
          label: "EPB"
        }
      ],
      value: "",
      errPoint:[],
      selectedBranch:""
      
    };
  },
  computed:{
    ...mapGetters(['getBranch'])
  },
  methods: {
    handleCheckAllChange(val) {
      this.checkedAuthority = val ? authorityOptions : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.authority.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.authority.length;
    },
    handleCheckAllChangeLager(val) {
      this.checkedAuthorityLager = val ? authorityOptionsLager : [];
      this.isIndeterminateLager = false;
    },
    handleCheckedCitiesChangeLager(value) {
      let checkedCount = value.length;
      this.checkAllLager = checkedCount === this.authorityLager.length;
      this.isIndeterminateLager =
        checkedCount > 0 && checkedCount < this.authorityLager.length;
    },
    sendNewUser(){
      this.errPoint=[]
      if(this.user.username == ""){
      setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Ungültiger Benutzername",
          type: "error",
            duration: 7000
        });
      }, 50);
        this.errPoint.push({err : 'Ungültiger Benutzername'})
      }
      if(this.user.password == ""){
       setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Ungültiges Passwort",
          type: "error",
            duration: 7000
        });
       }, 100);
        this.errPoint.push({err : 'Ungültiges Passwort'})
      }
     if(this.user.name == ""){
       setTimeout(() => {
         this.$notify({
          title: "Error",
          message: "ungültiger Name",
          type: "error",
            duration: 7000
        });
       }, 150);
        this.errPoint.push({err : 'ungültiger Name'})
      }
      if(this.user.lastname == ""){
     setTimeout(() => {
         this.$notify({
          title: "Error",
          message: "ungültiger Nachname",
          type: "error",
            duration: 7000
        });
     }, 200);
        this.errPoint.push({err : 'ungültiger Nachname'})
      }
      if(this.selectedBranch == ""){
     setTimeout(() => {
         this.$notify({
          title: "Error",
          message: "ungültiger Zweig",
          type: "error",
            duration: 7000
        });
     }, 250);
        this.errPoint.push({err : 'ungültiger Zweig'})
      }
      if(this.errPoint.length==0){
            let anlegen = false
            let stornieren = false
            let belegnrandern = false
            let belegandern = false
            let lagerlogin = false
            let lagerschreiben = false
            let lagerartikel = false
            let lagerzua = false
            let lagerstornoBeleg = false
            
            // console.log(this.checkedAuthority);
            

        this.checkedAuthority.forEach(res =>{
          if(res == "Auftrag anlegen" ){
             anlegen = true
          }
             if(res == "Auftrag stornieren" ){
             stornieren = true
          }
             if(res == "Belegnr. ändern" ){
             belegnrandern = true
          }
             if(res == "Beleg ändern" ){
               // console.log('GİRDİK BE');
               
             belegandern = true
          }
        })

         this.checkedAuthorityLager.forEach(res =>{
          if(res == "Lager Login" ){
             lagerlogin = true
          }
             if(res == "Lager Schreibrechte" ){
             lagerschreiben = true
          }
             if(res == "Lager Artikelverwaltung" ){
             lagerartikel = true
          }
             if(res == "Lager zurück auf Ablage" ){
             lagerzua = true
          }
             if(res == "Lager Storno Beleg" ){
             lagerstornoBeleg = true
          }
        })

        this.$store.dispatch('sendNewUser', {name : this.user.name, lastname : this.user.lastname, username : this.user.username, password : this.user.password, companyId : this.selectedBranch, isActive : true, isAdmin : false, isAnlegen : anlegen ,isStornieren:stornieren, isBelegAndern : belegnrandern, isBelegAndern:belegandern, isLagerLogin:lagerlogin, isLagerSchreiben:lagerschreiben, isLagerArtikel: lagerartikel, isLagerZuruck:lagerzua, isLagerStorneleg:lagerstornoBeleg})
        .then(result=>{
          if(result=='success'){
               this.$notify({
          title: "erfolgreich",
          message: "Der Benutzer wurde erfolgreich hinzugefügt.",
          type: "success",
            duration: 7000
        });
            this.$bvModal.hide('newUser')
          }
        })
      }
    }
  }
};
</script>

