export const dateFormatterConvertNum = (val) => {
 let newNum;
 if(val.includes('-'))
 {
      newNum = val.replace(/-/g, "");
 }
 else if(val.includes('.'))
 {
   newNum = val.replace(/./g, "");
 }
 newNum++;
return newNum
 
}


export const dataFormatterNaApi = (data) =>{

  let newDate = data.substr(0,10)
  let [year,month,date] = newDate.split('-')
  
  return date+'.'+month+'.'+year
}

export const dataFormatterApiFormat = (data) =>{

  let newDate = data.substr(0,10)
  let [year,month,date] = newDate.split('.')
  
  return date+'-'+month+'-'+year
}