            >{{ tag }}
            <i
              style="cursor:pointer;"
              @click="tagClose(index)"
              class="ml-1 fas fa-times-circle"
            ></i> </span
        ></span>
      </div>
      <el-row>
        <el-col :span="8" class="text-center mt-3">
          <strong
            class="text-primary"
            style="font-weight:bold!important;font-size: 18px"
