          </strong>
          <el-date-picker
            v-model="datePicker"
            type="date"
            placeholder="Pick a day"
            :picker-options="pickerOptions"
          ></el-date-picker>
        </el-col>
        <el-col class="mt-3" :offset="2" :span="6">
          <el-checkbox v-model="expresslieferung" border
            >Expresslieferung</el-checkbox
          >
        </el-col>
        <el-col
          :style="
          getActiveUser.length!=0?
            getActiveUser.companyId == 0
              ? ''
              : {
                  border: '1px solid black',
                  textAlign: 'center',
                  padding: '5px'
                }:''
          "
          :span="8"
          :class="
            getActiveUser.length!=0?
            getActiveUser.companyId == 0
              ? 'mt-3 text-right'
              : 'mt-3 shadow text-center':false
          "
        >
          <label>
            <strong style="font-weight:bold" class="mr-3">Filiale :</strong>
          </label>
          <strong style="font-weight:800">{{ selectedBranch }}</strong>
          <el-select
       
            v-if=" getActiveUser.length!=0?getActiveUser.companyId == 0:false"
            v-model="selectedBranch"
            placeholder="Select"
          >
            <el-option
              v-for="item in getBranch"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-col>
      </el-row>

      <div v-if="selectedDate!=null && selectedBranch!=null">
        <el-col :offset="2">
        <hr />
        <div class="form-group">
                {{goodsType1}}
          <el-checkbox
            @change="goodsType2 = false"
            label="Weißware"
            border
            v-model="goodsType1"
          ></el-checkbox>
          <el-checkbox
            @change="goodsType1 = false"
            label="Brauneware"
            border
            v-model="goodsType2"
          ></el-checkbox>
        </div>
        <hr />
      </el-col>
      <el-col :offset="2" :span="21">
        <div class="form-group border p-2 shadow">
          <label>
            <strong style="font-weight:bold" class="mr-3">Uhrzeit :</strong>
          </label>
          <el-select @change="controlClaim" v-model="selectedTime" placeholder="Select">
            <el-option
              v-for="item in resultOptions"
              :key="item.value"
              :label="item.label"
              :disabled="item.disabled"
              :value="item.value"
            ></el-option>
          </el-select>
          <strong
            v-if="selectedDate != ''"
            style="font-weight:bold"
            class="ml-3 border p-2 border-success text-success"
            >Datum : {{ selectedDate }}</strong
          >
        </div>
      </el-col>
      <components
      :expresslieferung="expresslieferung"
        :isWhite="goodsType1"
        :isBraun="goodsType2"
        @selectedTypeItems="selectedType = $event"
        @oldMach="OldAppliance = $event"
        @chooseLager="chooseLager = $event"
        @checked="checkedAll = $event"
        :is="goodsType1?'newProductWei':'newProductBraun'"></components>
      <!-- <new-product-wei
        :isWhite="goodsType1"
        @selectedTypeItems="selectedType = $event"
        @oldMach="OldAppliance = $event"
        @chooseLager="chooseLager = $event"
        @checked="checkedAll = $event"
        v-if="goodsType1"
      ></new-product-wei>

      <new-product-braun
        :isWhite="goodsType1"
        @selectedTypeItems="selectedType = $event"
        @oldMach="OldAppliance = $event"
        @chooseLager="chooseLager = $event"
        @checked="checkedAll = $event"
        v-else
      ></new-product-braun> -->
      <el-col :offset="2" :span="21">
        <div class="form-group border shadow p-3">
              <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model.lazy="form.beleg"  type="text" required />
        <label> Beleg nr. </label>
        <span style="position:absolute;right:0">
         <el-button round size="small" :type="usableBeleg?'success':'danger'"><i :class="usableBeleg?'el-icon-check':'el-icon-close'"></i></el-button>

        </span>
      </div>
    </div>
       
          <el-row>
            <el-col :xs="24" :sm="24" :md="10">
              <eralp-form @inputVModel="form.name = $event">Vorname</eralp-form>
            </el-col>
            <el-col :xs="22" :sm="22" :md="10" :offset="2">
              <eralp-form @inputVModel="form.lastname = $event"
                >Nachname</eralp-form
              >
            </el-col>
          </el-row>
          <eralp-form @inputVModel="form.address = $event">Straße</eralp-form>
          <eralp-form @inputVModel="form.plz = $event">PLZ. Ort</eralp-form>
          <eralp-form @inputVModel="form.phone = $event">Telefon</eralp-form>
        </div>
      </el-col>
      <el-col :offset="6" :span="12">
        <div class="form-group border shadow p-3">
          <eralp-form @inputVModel="form.zusatz = $event">Zusatz</eralp-form>
        </div>
      </el-col>
      <el-col class="mt-5" :offset="8" :span="8">
        <div class="form-group">
          <button
            :disabled="!usableBeleg"
            @click="sendNewProduct()"
            class="btn btn-outline-primary w-100"
          >
            Speichern
          </button>
        </div>
      </el-col>
      </div>
    </el-row>
  </b-modal>
</template>

<script>
import { dateFormatterElement, dateFormatter } from "../../dateFormatter";
import newProductWei from "../Modals/newProductModalItems/newProductWeißeware";
import newProductBraun from "../Modals/newProductModalItems/newProductBrauneware";
import eralpForm from "../eralpComponents/eralp-form";
import { mapGetters } from "vuex";
import { postApi, getApi, getApiHelper, postApiNa } from '../../api/api';
const cityOptions = [
  "Ausbau AG 10, -Euro",
  "Aufbau Waschtrockensaeule",
  "Tueranschlagwechsel bis 85cm"
];
const cityOptionsPremium = [
  "Unterbau Geschirspueler",
  "Unterbau Kuehlschrank",
  "Unterbau Waschmaschiene",
  "Tueranschlag wechsel",
  "Lieferung Side by Side",
  "Anschluss Standherd",
  "Anschluss Einbauherd",
  "Einbau Geschirspueler",
  "Einbau Kuehlschrank",
  "Einbau Waschmaschiene",
  "Montage Dunstabzugshaube"
];
const options = [
  {
    value: "8-12",
    label: "8-12",
    disabled : false
  },
  {
    value: "10-14",
    label: "10-14",
    disabled : false

  },
  {
    value: "12-16",
    label: "12-16",
    disabled : false

  },
  {
    value: "14-18",
    label: "14-18",
    disabled : false

  },
  {
    value: "16-20",
    label: "16-20",
    disabled : true

  }
];
const optionsExpress = [
  {
    value: "10-11",
    label: "10-11"
  },
  {
    value: "11-12",
    label: "11-12"
  },
  {
    value: "12-13",
    label: "12-13"
  },
  {
    value: "13-14",
    label: "13-14"
  },
  {
    value: "14-15",
    label: "14-15"
  },
  {
    value: "15-16",
    label: "15-16"
  },
  {
    value: "16-17",
    label: "16-17"
  }
];
function resComp() {
  return {
     errPoint: [],
      tagGreen: {
        padding: "8px",
        background: "rgb(181, 255, 181)",
        color: "green",
        borderRadius: "20px",
        margin: "0 5px"
      },
      tagRed: {
        padding: "8px",
        background: "rgb(253, 167, 167)",
        color: "red",
        borderRadius: "20px",
        margin: "0 5px",
        fontWeight: 800
      },
      tagBlue: {
        padding: "8px",
        background: "rgb(185, 180, 255)",
        color: "white",
        borderRadius: "20px",
        margin: "0 5px",
        fontWeight: 800
      },
      tagYellow: {
        padding: "8px",
        background: "rgb(255, 255, 162)",
        color: "black",
        borderRadius: "20px",
        margin: "0 5px"
      },
      tagOrange: {
        padding: "8px",
        background: "orange",
        color: "black",
        borderRadius: "20px",
        margin: "0 5px"
      },
      tagPurple: {
        padding: "8px",
        background: "rgb(255, 141, 255)",
        color: "black",
        borderRadius: "20px",
        margin: "0 5px"
      },
      form: {
        beleg: "",
        name: "",
        lastname: "",
        address: "",
        plz: "",
        telefon: ""
      },
      optionsSelect: cityOptions,
      checkAll: false,
      checkedAll: [],
      checkAllPremium: false,
      checkedCities: [],
      checkedCitiesPrmeium: [],
      cities: cityOptions,
      optionsPremium: cityOptionsPremium,
      isIndeterminate: true,
      isIndeterminatePremium: true,
      chooseLager: "Außenlager",
      selectedTime: "",
      OldAppliance: false,
      selectedDate: null,
      selectedBranch: null,
      selectedType: "",
      usableBeleg:null,
      searchBranch:null,
      success:false,
      tags: {
        selectedType: "",
        express: "",
        lager: "Außenlager",
        zeit: "",
        date: "",
        department: "",
        oldMach: "",
        branch: "",
        check: []
      },
      optionsType: [
        { value: "Komfort", label: "Komfort" },
        { value: "Premium", label: "Premium" }
      ],

      datePicker: null,
      resultOptions: options,
      expresslieferung: false,
      goodsType1: null,
      goodsType2: null,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() <= Date.now();
        },
        shortcuts: [
          {
            text: "Heute",
            onClick(picker) {
              picker.$emit("pick", new Date());
               }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nächste Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
  }
}
export default {
  components: {
    eralpForm,
    newProductWei,
    newProductBraun
  },
  data: resComp,
  computed: {
    ...mapGetters(["getBranch", "getActiveUser",'getToday'])
  },
  watch: {
    success(data){
      if(data){
     setTimeout(() => {
                 Object.assign(this.$data, resComp())
     }, 3000);
      }
    },
    expresslieferung(){
        for (let i = 0; i < optionsExpress.length; i++) {
          optionsExpress[i].disabled =false
          
        }
      if(this.selectedDate!=null && this.selectedBranch!=null){
          this.expressControl(this.selectedDate)
      }
    },
    'form.beleg'(data){
     if(data!=""){
        getApi('/orders/beleg/'+data).then(result=>{
        if(result.count!=0){
          this.usableBeleg=false
        }
        else{
          this.usableBeleg = true
        }
      })
     }
    },
    getToday(){
      // // console.log('work',this.getToday);
    if(this.getToday>13)
    {
      this.selectedDate=""
      this.datePicker=null
          // // console.log('girdik');
      this.pickerOptions.shortcuts = [
          {
            text: "Heute",
            onClick(picker) {
               const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
               }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nächste Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
    }
    else {
      this.selectedDate=""
       this.pickerOptions.shortcuts = [
          {
            text: "Heute",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nächste Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
    }
    },
    selectedBranch(data) {
      // console.log(data);
       setTimeout(() => {
   if(this.getActiveUser.length!=0){
        if (this.getActiveUser.companyId != 0) {
        this.selectedBranch = this.getActiveUser.companyId;
      } }
    }, 2000);
      
      if(data==1 || data==2)
      { this.searchBranch =data
        // console.log('1 2 ');
        
      } 
      else { this.searchBranch=3
        // console.log('1 2 değil');
      }
        for (let i = 0; i < optionsExpress.length; i++) {
          optionsExpress[i].disabled =false
          
        }
      if(this.selectedDate!=null){
        this.expressControl(this.selectedDate)
      }
      this.getBranch.forEach(result=>{
        if(this.selectedBranch==result.id){
          this.tags.branch = result.name
        }
      })
    },
    checkedAll() {
      this.tags.check = this.checkedAll;
    },
    OldAppliance() {
      if (this.OldAppliance) {
        this.tags.oldMach = "Algerat";
      } else {
        this.tags.oldMach = "";
      }
    },
    selectedTime() {
      this.tags.zeit = this.selectedTime;
    },
    selectedDate() {
      this.tags.date = this.selectedDate;
    },
    goodsType1() {
      if (this.goodsType1) {
        this.tags.department = "Weißware";
      }
      else if(!this.goodsType1 && !this.goodsType2){
            this.goodsType1 = null
      }
      if (this.goodsType1==null) {
        this.tags.department = "";
      }
    },
    goodsType2() {
      if (this.goodsType2) {
        this.tags.department = "Brauneware";
      }
      else if(!this.goodsType1 && !this.goodsType2){
            this.goodsType1 = null
            this.goodsType2 = null
      }
      if (this.goodsType2==null) {
        this.tags.department = "";
      }
    },
    chooseLager() {
      this.tags.lager = this.chooseLager;
    },
    expresslieferung() {
      if (this.expresslieferung) {
        this.tags.express = "Express";
        this.resultOptions = optionsExpress;
        this.selectedTime = "";
      } else {
        this.tags.express = "";

        this.resultOptions = options;
        this.selectedTime = "";
      }
    },

    selectedType() {
      // // console.log(this.selectedType, "watch");
      this.tags.selectedType = this.selectedType;
      this.selectedType == "Premium"
        ? (this.optionsSelect = cityOptionsPremium)
        : (this.optionsSelect = cityOptions);
    },
    datePicker(event) {
      // // console.log(event);
        for (let i = 0; i < optionsExpress.length; i++) {
          optionsExpress[i].disabled =false
          
        }
      if (event != null) {
       
        let day = event.getDay()
        // console.log(day);
        if(day==2 || day==4){
          options[4].disabled=false
           this.selectedDate = dateFormatterElement(event);
            this.expressControl( this.selectedDate)
        }
        else if(day==0){
          this.datePicker=null
          this.selectedDate=null
            this.$notify({
          title: "Error",
          message: "Sonntags arbeiten wir nicht",
          type: "error"
        });
        }else{
            options[4].disabled=true
             this.selectedDate = dateFormatterElement(event);
                    this.expressControl( this.selectedDate)
        }
        
        
       
       // this.selectedDate = dateFormatter(this.selectedDate);
      } else {
        this.selectedDate = null;
           options[4].disabled=true
      }
    }
  },
  methods: {

    controlClaim(data){
     postApiNa('ghs/claimHelper',{ companyId: this.searchBranch, date: this.selectedDate, time :data }).then(result=>{
       // console.log(result.data[0]["SUM(hak)"]);
       if(result.data[0]["SUM(hak)"]==0){
         this.selectedType==null
            this.$notify({
          title: "Error",
          message: data+" - Sie haben keine Kundenrechte",
          type: "error",
              duration: 7000

        });
       }
     })
      
    },
    expressControl(data){
      if(data!=null && this.searchBranch!=null){
            if(this.searchBranch==1  || this.searchBranch==2){
                getApi('orders?filter=setupDate:"'+data+'",companyId:'+this.searchBranch).then(res=>{
                
                  
        if(res.body!=null){
          res.body.forEach(body=>{
            // console.log(body.setupTime);
            if(body.setupTime=='10-11'){
              optionsExpress[0].disabled=true
            }
            if(body.setupTime=='11-12'){
              // console.log('girdik');
              
              optionsExpress[1].disabled=true
            }
            if(body.setupTime=='12-13'){
              optionsExpress[2].disabled=true
            }
            if(body.setupTime=='13-14'){
              optionsExpress[3].disabled=true
            }
            if(body.setupTime=='14-15'){
              optionsExpress[4].disabled=true
            }
            if(body.setupTime=='15-16'){
              optionsExpress[5].disabled=true
            }
            if(body.setupTime=='16-17'){
              optionsExpress[6].disabled=true
            }
            
          })
        }
      })
            }
            else {
                getApi('orders?filter=setupDate:"'+data+'",companyId:3||companyId:4||companyId:5||companyId:6||companyId:7||companyId:8||companyId:9||companyId:10').then(res=>{
        if(res.body!=null){
          res.body.forEach(body=>{
            // console.log(body.setupTime);
            if(body.setupTime=='10-11'){
              optionsExpress[0].disabled=true
            }
            if(body.setupTime=='11-12'){
              // console.log('girdik');
              
              optionsExpress[1].disabled=true
            }
            if(body.setupTime=='12-13'){
              optionsExpress[2].disabled=true
            }
            if(body.setupTime=='13-14'){
              optionsExpress[3].disabled=true
            }
            if(body.setupTime=='14-15'){
              optionsExpress[4].disabled=true
            }
              if(body.setupTime=='15-16'){
              optionsExpress[5].disabled=true
            }
            if(body.setupTime=='16-17'){
              optionsExpress[6].disabled=true
            }
            
          })
        }
      })
            }
      }
      else {
        for (let i = 0; i < optionsExpress.length; i++) {
          optionsExpress[i].disabled =false
          
        }
      }
    },
    tagClose(index) {
      this.tags[index] = "";
      if (index == "date") {
        this.datePicker = null;
      } else if (index == "zeit") {
        this.selectedTime = "";
      } else if (index == "express") {
        this.expresslieferung = false;
      } else if (index == "branch") {
        this.selectedBranch = ""; //oldMach
      } else if (index == "department") {
        this.goodsType1 = false;
        this.goodsType2 = false;
      } else if (index == "oldMach") {
        this.OldAppliance = false;
      }
    },
    tagCloseCheck(index) {
      this.checkedAll[index] = null;
      this.tags.check[index] = null;
    },
    handleCheckAllChange(val) {
      this.selectedType == "Premium"
        ? (this.optionsSelect = cityOptionsPremium)
        : (this.optionsSelect = cityOptions);
      this.checkedCities = val ? this.optionsSelect : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    },
    sendNewProduct() {
      this.errPoint = [];
      let goods = "";
      let isWhite=false
      //// // console.log(this.form,this.selectedTime,this.selectedDate,this.checkedAll);
      if (this.goodsType1) {
        goods = "Weißware";
        isWhite = true
}
      if (this.goodsType2) {
        goods = "Brauneware";
        isWhite= false
      }
      if (this.chooseLager == "") {
        setTimeout(() => {
            this.$notify({
          title: "Error",
          message: "Lager",
          type: "error"
        });
        }, 150);
        this.errPoint.push({ err: "Lager" });
      }
      if (goods == "") {
       setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Weißware or Brauneware",
          type: "error",
            duration: 7000
        });
       }, 100);
        this.errPoint.push({ err: "Weißware or Brauneware" });
      }
      if (this.form.beleg == "") {
        this.errPoint.push({ err: "Beleg Num" });
      setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Beleg Num",
          type: "error",
              duration: 7000

        });
      }, 50);
      }
      if (this.form.name == "") {
     setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Vorname",
          type: "error",
              duration: 7000

        });
     }, 200);
        this.errPoint.push({ err: "Vorname" });
      }
      if (this.form.lastname == "") {
       setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Nachname",
          type: "error",
              duration: 7000

        });
       }, 250);
        this.errPoint.push({ err: "Nachname" });
      }
      if (this.form.address == "") {
    setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Straße",
          type: "error",
              duration: 7000
        });
    }, 300);
        this.errPoint.push({ err: "Straße" });
      }
      if (this.form.plz == "") {
        setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "PLZ",
          type: "error",
           duration: 10000
        });
        }, 350);
        this.errPoint.push({ err: "PLZ" });
      }
      if (this.form.phone == "") {
        setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Telefon",
          type: "error",
           duration: 10000
        });
        }, 400);
        this.errPoint.push({ err: "Telefon" });
      }
      if (this.selectedDate == "") {
     setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Datum",
          type: "error",
           duration: 10000
        });
     }, 450);
        this.errPoint.push({ err: "Datum" });
      }
      if (this.selectedTime == "") {
       setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Zeit",
          type: "error",
           duration: 10000
        });
       }, 500);
        this.errPoint.push({ err: "Zeit" });
      }
      if (this.selectedType == "") {
       setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Art auswählen",
          type: "error",
          duration: 10000
        });
       }, 550);
        this.errPoint.push({ err: "Art auswählen" });
      }
      if (this.selectedBranch == "") {
        setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Filiale",
          type: "error",
           duration: 10000
        });
        }, 600);
        this.errPoint.push({ err: "Filiale" });
      }
      let isHaus =false
      let isAus = false
      if(this.chooseLager=='Hauslager') {  isHaus = true }
      if(this.chooseLager=='Außenlager') {  isAus = true }
      if(this.chooseLager=='Mischlager') 
      {  isAus = true
        isHaus=true }
      if (this.errPoint.length == 0) {
        this.$store.dispatch("sendNewCustomer", {
          userId: this.getActiveUser.id,
          isOldMachine: this.OldAppliance,
          isExpress: this.expresslieferung,
          isHaus: isHaus,
          isAus :isAus,
          status: "Nsubmitted",
       //   isWhite : isWhite,
          beleg: this.form.beleg,
          customerName: this.form.name,
          customerLastname: this.form.lastname,
          customerAddress: this.form.address,
          zipCode: this.form.plz,
          companyId : this.selectedBranch,
          customerPhone: this.form.phone,
          setupDate: this.selectedDate,
          setupTime: this.selectedTime,
        //  setupDefinitionId: this.selectedType,
         // options: this.checkedAll,
          orderNote: this.form.zusatz,
          isActive: true
        }).then(result=>{
          if(result.status="success"){
            postApi('orders_SetupDefinition',{ orderId : result.id , setupDefinitionId : this.selectedType}).then(response=>{
                if(response.result!=null){
                  this.checkedAll.forEach(all=>{
            postApi('orders_SetupDefinition',{ orderId : result.id , setupDefinitionId : all}).then(response=>{
                  // console.log(response);
                            this.$refs["newProduct"].hide();
                            this.success=true
            })
                  })
                }
            })
          }
        })

         
      }
      
    }
  },
  mounted() {
    setTimeout(() => {
   if(this.getActiveUser.length!=0){
        if (this.getActiveUser.companyId != 0) {
        this.selectedBranch = this.getActiveUser.companyId;
      } }
    }, 2000);
  },

};
</script>

