import { getApi,postApi, putApi, postApiNa } from "../../api/api";
import {dateFormatter, dataFormatterNaApi} from '../../dateFormatter'

const state = {
   customersAll:[
       ],
       customers:[],
       customersBranch:[]
}

const getters= {
  getCustomers(state){
    return state.customersAll;
  }
}
const mutations= {
  
  setNewCustomer(state,payload){
      // // console.log(payload);

      payload.setupDate = dateFormatter(payload.setupDate)
    state.customersAll.unshift({...payload})
    // // console.log(state.customersAll);
    
  //  state.customersAll.unshift(payload)
  },
  setCustomersForBranch(state,payload){
    state.customers=[]  
    // // console.log(payload,'work');
   state.customersAll.filter(response=>{
    if(payload.companyId == 0)
    {
      state.customers = state.customersAll
    }
    else  if(response.branch == payload.companyId && response.isActive)
    {
      state.customers.push(response)
    }
   })

    
  },
  setOrderTable(state,payload){
    if(payload.data==null){
    state.customersAll = []
    }
    else{
      payload.data.forEach(data=>{
        data.setupDate = dataFormatterNaApi(data.setupDate)
      })
    state.customersAll = payload.data
    }
  }
}

const actions= {
  initOrdersTable(VuexContext,payload){
    return postApiNa('ghs/orderList' , {companyId: payload}).then(response=>{
      if(!response.status){
        return 'fail'
      }
      else{
         VuexContext.commit('setOrderTable',response)
      return 'success'
      }
     
    })
  },

  sendNewCustomer(VuexContext,payload){
    // console.log(payload);
    
  return postApi('orders',payload).then(result=>{
     // // console.log(result);
     
     VuexContext.commit('setNewCustomer', {...payload, id:result.result})
     return { status :"success", id : result.result }
    // // console.log(payload);
    
   })
},
sendPutCustomer(veuxcontext,payload){
  // // console.log(payload);
  
  putApi('orders',payload.id, {
  beleg : payload.beleg,
  companyId : payload.companyId,
  customerAddress :  payload.customerAddress,
  customerLastname : payload.customerLastname,
  customerName : payload.customerName,
  customerPhone : payload.customerPhone,
  zipCode : payload.zipCode,
  isAus: payload.isAus,
  isHaus : payload.isHaus,
  isExpress: payload.isExpress,
  orderNote: payload.orderNote,
  isOldMachine : payload.isOldMachine,
  
  }).then(result=>{
    // console.log(result);
    
  })
}
}

export default{
    state,
    getters,
    mutations,
    actions
}
