import Vue from 'vue'
import Vuex from 'vuex'
import { getApi, putApi, postApi } from '../../api/api';

Vue.use(Vuex);

const state={
    packageDescriptionTable: [
 
      ],
}
const getters= {
    getPackageDescriptionTable(state){
        return state.packageDescriptionTable;
    }
   
}
const mutations= {
    setPushPackageDescriptionTable(state,payload){
        state.packageDescriptionTable.unshift(payload)
    },
    setPackageDescription(state,payload){
        state.packageDescriptionTable = payload.body
        // console.log(payload);
        
    }
}

const actions= {
    initPackageDescription(VuexContext){
       return getApi('setupDefinition?filter=name:"0"').then(response=>{
            VuexContext.commit('setPackageDescription',response)
            return 'success'
        })
    },
   pushPackageDescriptionTable(VuexContext,payload){
    // console.log(payload);
       
    //APİ İŞLEMLERİ 
       return postApi('setupDefinition',payload).then(response=>{
               // console.log(payload);
        
     VuexContext.commit('setPushPackageDescriptionTable',payload)
      return 'success'
        })
     
      
    },
    sendEditPackageDescription(VuexContext,payload){
        // console.log(payload);
        return putApi('setupDefinition',payload.id,{ description: payload.description, isWhite:payload.isWhite})
        .then(res=>{
            // console.log(res);
            return 'success'
            
        })
   
       
    }
    
}

export default{
    state,
    getters,
    mutations,
    actions
}