<template>
  <div class="text-center p-5"> 
      <el-table
     v-if="!loading"
    :data="definition"
    border
    style="width: 100%;font-weight:800;font-size:18px">
        <el-table-column
      prop="isWhite"
      label="Abteilung">
      <template slot-scope="scope">
          {{scope.row.isWhite?'Weißware':'Braunerware'}}
      </template>
    </el-table-column>
    <el-table-column
      prop="parentName"
      label="Tarife">
      <template slot-scope="scope">
          {{scope.row.parentName}}
      </template>
    </el-table-column>
    <el-table-column
      prop="name"
      label="Name">
    </el-table-column>
        <el-table-column
      prop="isEdit"
      label="Edit">
      <template slot-scope="scope">
         <div @click="tarife={ name :scope.row.name, id: scope.row.id, index: scope.$index}" v-b-modal.editTarifeTable>
              <i  class="el-icon-edit rounded-circle bg-warning text-dark p-2" style="font-size:24px"></i>
         </div>
      </template>
    </el-table-column>
  </el-table>
     <b-modal id="editTarifeTable" ref="editTarifeTable" size="small" hide-footer title="Edit Tarife Table">
   <div v-if="tarife!=null">
         <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="tarife.name"  type="text" required />
        <label> Tarife </label>
      
      </div>
    </div>
    <div class="text-right">
        <el-button @click="sendEditTarife()" type="primary" round>Speichern</el-button>
    </div>
   </div>
     </b-modal>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import auth from '../mixins/auth';
import isAdmin from '../mixins/isGod';
import { putApi } from '../api/api';
export default {
      mixins:[auth,isAdmin],
      data(){return { definition:[], tarife:null , loading:false}},
computed:{
    ...mapGetters(['getDefinition','getTarifeTable'])
},
watch:{
  getDefinition(){
    console.log('değişti');
    
   if(this.getDefinition.length!=0){
      this.loading=true
    setTimeout(() => {
       this. definition = this.getDefinition
    this.definition.forEach((def)=>{
           this.getTarifeTable.forEach(parent=>{
               if(def.parentId==parent.id){
                 def.parentName = parent.name
                    if(parent.isWhite){
                        def.iswhite = true
                    }
                    else {
                        def.iswhite=false
                    }
               }
           })
       })
      this.loading=false
    }, 250);
   }
   else{
     this.definition = []
   }
  },
},
methods:{
    sendEditTarife(){
        this.loading=true
        putApi('setupDefinition',this.tarife.id,{ name : this.tarife.name }).then(result=>{
            console.log(result);
            if(result.result==1){
                this.$notify({
              title: "Erfolg",
                  message: "Die Beschreibung wurde erfolgreich hinzugefügt.",
              type: "success"
                      
        });
        this.loading=false
        this.$store.state.branch.definition[this.tarife.index].name = this.tarife.name
        this.$refs['editTarifeTable'].hide()
            }
            
        })
    }
},
mounted(){
    this.loading=true
  if(this.getDefinition.length==0){
    this.$store.dispatch('initSetupDefinitionTable')
  }

    this. definition = this.getDefinition
this.definition.forEach((def)=>{
           this.getTarifeTable.forEach(parent=>{
               if(def.parentId==parent.id){
                 def.parentName = parent.name
                    if(parent.isWhite){
                        def.iswhite = true
                    }
                    else {
                        def.iswhite=false
                    }
               }
           })
       })
       this.loading=false
}

}
</script>

