<template>
  <b-modal id="editCustomer2" ref="editCustomer2" size="xl"  hide-footer>
    <el-row v-if="show">
      <el-col
        :class="edit ? 'transition':'transition'"
        :xs="22"
        :sm="22"
        :md="22"
        class="text-right"
      >
        <i
        
          v-if="!edit && !loading"
          @click="edit=true"
          class="el-icon-edit h3 p-2 px-3 rounded-circle border bg-warning text-dark hoverEdit"
        ></i>
         <i
        
          v-if="edit"
          @click="edit=false"
          class="el-icon-close h3 p-2 px-3 rounded-circle border bg-warning text-dark hoverEdit"
        ></i>
        <i
        v-if="editCustomer.isActive && getActiveUser.isStornieren"
          @click="deleteCustomer"
          class="el-icon-delete h3 mx-2 p-2 px-3 rounded-circle border bg-danger text-white hoverDelete"
        ></i>
      </el-col>
      <el-col
        :class="edit ? 'transitionSuccess':'transitionSuccess'"
        v-if="edit"
        :offset="edit?18:0"
        :xs="2"
        :sm="2"
        :md="2"
      >
      </el-col>
    </el-row>
    <el-row v-if="edit">
      <el-col :offset="3">
        <div class="form-group">
          <el-checkbox @change="goodsType2=false" label="Weißware" border v-model="goodsType1"></el-checkbox>
          <el-checkbox @change="goodsType1=false" label="Braunware" border v-model="goodsType2"></el-checkbox>
        </div>
        <hr />
      </el-col>
      <el-col :offset="3" :span="21">
      </el-col>
            <components
            @oldOptions="oldOptions=$event"
        :isWhite="goodsType1"
        :editCustomer="editCustomer"
        :isBraun="goodsType2"
        @selectedTypeItems="selectedType = $event"
        @oldMach="OldAppliance = $event"
        @chooseLager="chooseLager = $event"
        @checked="checkedAll = $event"
        :is="goodsType1?'newProductWei':'newProductBraun'"></components>


      <el-col :offset="3" :span="21">
        <div class="form-group border shadow p-3">
          <div class="eralp-form-control">
            <div class="eralp-input-container">
              <input :readonly="!getActiveUser.isBelegNrAndern" v-model="editCustomer.beleg" type="text" required />
              <label>Belegnr.</label>
            </div>
          </div>
          <el-row>
            <el-col :xs="24" :sm="24" :md="10">
              <div class="eralp-form-control">
                <div class="eralp-input-container">
                  <input v-model="editCustomer.customerName" type="text" required />
                  <label>Vorname</label>
                </div>
              </div>
            </el-col>
            <el-col :xs="24" :sm="24" :md="2" class="p-1"></el-col>
            <el-col :xs="24" :sm="24" :md="10" >
              <div class="eralp-form-control">
                <div class="eralp-input-container">
                  <input v-model="editCustomer.customerLastname" type="text" required />
                  <label>Nachname></label>
                </div>
              </div>
            </el-col>
          </el-row>
          <div class="eralp-form-control">
            <div class="eralp-input-container">
              <input v-model="editCustomer.customerAddress" type="text" required />
              <label>Straße</label>
            </div>
          </div>
          <div class="eralp-form-control">
            <div class="eralp-input-container">
              <input v-model="editCustomer.zipCode" type="text" required />
              <label>PLZ</label>
            </div>
          </div>
          <div class="eralp-form-control">
            <div class="eralp-input-container">
              <input v-model="editCustomer.customerPhone" type="text" required />
              <label>Telefon</label>
            </div>
          </div>
        </div>
      </el-col>
      <el-col :offset="6" :span="12">
        <div class="form-group border shadow p-3">
          <div class="eralp-form-control">
            <div class="eralp-input-container">
              <input v-model="editCustomer.orderNote" type="text" required />
              <label>Zusatz</label>
            </div>
          </div>
        </div>
      </el-col>
      <el-col class="mt-5" :offset="8" :span="8">
        <div class="form-group">
          <button @click="editCustomerApi()" class="btn btn-outline-primary w-100">Speichern</button>
        </div>
      </el-col>
    </el-row>
    <div v-else>
      <el-row v- class="text-center">
        <el-col>
          <h3 class="text-primary" style="text-decoration:underline;font-weight:700">KUNDE</h3>
        </el-col>
        <el-col :xs="24" :sm="24" :md="24">
          <section class="p-1 my-2">
            <strong style="font-weight:700">Vollständiger Name :</strong>
            {{editCustomer.customerName}} {{editCustomer.customerLastname}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Straße:</strong>
            {{editCustomer.customerAddress}} {{editCustomer.zipCode}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Telefon :</strong>
            {{editCustomer.customerPhone}}
          </section>
        </el-col>
      </el-row>
      <el-row style="font-size:16px;">
        <el-col>
          <h3
            class="text-center text-primary"
            style="text-decoration:underline;font-weight:700"
          >Lieferung</h3>
        </el-col>
        <el-col class="text-center" :xs="24" :sm="24" :md="12">
          <section class="p-1 my-2 text-danger">
            <strong style="font-weight:700">Beleg Nr. :</strong>
            {{editCustomer.beleg}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Lager:</strong>
            {{editCustomer.isHaus&&editCustomer.isAus?'Mischlager':editCustomer.isHus?'Hauslager':'Außenlager'}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Datum :</strong>
            {{editCustomer.setupDate}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Uhrzeit :</strong>
            {{editCustomer.setupTime}}
          </section>
        </el-col>
        <el-col class="text-center" :xs="24" :sm="24" :md="12">
          <section class="p-1 my-2">
            <strong style="font-weight:700">Tarif :</strong>
            {{editCustomer.setupDefinitionId}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Altgerat:</strong>
            {{editCustomer.isOldMachine ? 'Ja': 'Nein'}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Abteilung :</strong>
            {{editCustomer.isWhite?'Weißware':'Brauneware'}}
          </section>
          <section class="p-1 my-2">
            <strong style="font-weight:700">Express :</strong>
            {{editCustomer.isExpress ? 'Ja':'Nein'}}
          </section>
        </el-col>
      </el-row>
    </div>

    <hr />
  </b-modal>
</template>

<script>
import { dateFormatterElement, dateFormatter } from "../../../dateFormatter";
import newProductWei from "../newProductModalItems/newProductWeißeware";
import newProductBraun from "../newProductModalItems/newProductBrauneware";
import { mapGetters } from 'vuex';
import { deleteApi, putApi, getApi, postApi } from '../../../api/api';
const cityOptions = [
  "Ausbau AG 10, -Euro",
  "Aufbau Waschtrockensaeule",
  "Tueranschlagwechsel bis 85cm"
];
const cityOptionsPremium = [
  "Unterbau Geschirspueler",
  "Unterbau Kuehlschrank",
  "Unterbau Waschmaschiene",
  "Tueranschlag wechsel",
  "Lieferung Side by Side",
  "Anschluss Standherd",
  "Anschluss Einbauherd",
  "Einbau Geschirspueler",
  "Einbau Kuehlschrank",
  "Einbau Waschmaschiene",
  "Montage Dunstabzugshaube"
];
const options = [
  {
    value: "8-12",
    label: "8-12"
  },
  {
    value: "10-14",
    label: "10-14"
  },
  {
    value: "12-16",
    label: "12-16"
  },
  {
    value: "14-18",
    label: "14-18"
  }
];
const optionsExpress = [
  {
    value: "10-11",
    label: "10-11"
  },
  {
    value: "11-12",
    label: "11-12"
  },
  {
    value: "12-13",
    label: "12-13"
  },
  {
    value: "13-14",
    label: "13-14"
  },
  {
    value: "14-15",
    label: "14-15"
  },
  {
    value: "15-16",
    label: "15-16"
  },
  {
    value: "16-17",
    label: "16-17"
  }
];
export default {
  props: ["editCustomer"],
  components: {
    newProductWei,
    newProductBraun
  },
  computed:{
    ...mapGetters(['getActiveUser'])
  },
  data() {
    return {
      oldOptions:[],
      loading:true,
      edit: false,
      form: {
        belegNo: "",
        name: "",
        lastname: "",
        address: "",
        plz: "",
        telefon: ""
      },
      optionsSelect: cityOptions,
      checkAll: false,
      show:true,
      checkAllPremium: false,
      checkedCities: [],
      checkedCitiesPrmeium: [],
      cities: cityOptions,
      optionsPremium: cityOptionsPremium,
      isIndeterminate: true,
      isIndeterminatePremium: true,
      chooseLager: "Außenlager",
      selectedTime: "",
      OldAppliance: false,
      selectedDate: "",
      selectedType: "Komfort",
      optionsType: [
        { value: "Komfort", label: "Komfort" },
        { value: "Premium", label: "Premium" }
      ],

      value2: "",
      resultOptions: options,
      expresslieferung: false,
      goodsType1: false,
      goodsType2: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() <= Date.now();
        },
        shortcuts: [
          {
            text: "Heute",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nächste Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  watch: {

    OldAppliance(data){ if(data){ this.editCustomer.isOldMachine=true} else { this.editCustomer.isOldMachine =false}
    },
    chooseLager(data){
     if(data=='Hauslager'){ this.editCustomer.isHaus=true; this.editCustomer.isAus=false}
     if(data=='Außenlager'){ this.editCustomer.isHaus=false; this.editCustomer.isAus=true}
     if(data=='Mischlager'){ this.editCustomer.isHaus=true; this.editCustomer.isAus=true}
       
    },
    goodsType1(data){
      // console.log(data);
      
      if(data){ this.isWhite = true} else { this.isWhite=false}
    },
    editCustomer() {
      this.edit=false
      this.show=false
      console.log('değişti');
      
         getApi('orders_SetupDefinition?filter=orderId:'+this.editCustomer.id).then(result=>{
           console.log(result);
           
     if(result.body!=null){
       let tarifeId = result.body[result.body.length-1].setupDefinitionId
        getApi('setupDefinition?filter=id:'+tarifeId).then(res=>{
          console.log(result,'definiton',res);
          
          if(res.body!=null){
            this.editCustomer.setupDefinitionId = res.body[0].name
            console.log(this.editCustomer.setupDefinitionId);
            this.show=true
       
            if (res.body[0].isWhite) {
        this.goodsType1 = true;
        this.goodsType2 = false;
      } else {
        this.goodsType1 = false;
        this.goodsType2 = true;
      }
           this.loading=false
          }
        })
      
     }
   })

      if (this.editCustomer.isExpress) {
        this.resultOptions = optionsExpress;
        this.selectedTime = "";
      } else this.resultOptions = options;
      this.selectedTime = "";
    },
    "editCustomer.isExpress"() {
      if (this.editCustomer.isExpress) {
        this.resultOptions = optionsExpress;
        this.selectedTime = "";
      } else this.resultOptions = options;
      this.selectedTime = "";
    },
    selectedType(data) {
      // console.log(Number(data));
      
     if(Number.isInteger(data)){ 
     this.editCustomer.setupDefinitionId = data
      
     } else { // console.log(data,'İSNAN');
     }
    },
    value2(event) {
      // console.log(event);
      if (event != null) {
        this.selectedDate = dateFormatterElement(event);
       // this.selectedDate = dateFormatter(this.selectedDate);
      } else {
        this.selectedDate = "";
      }
    }
  },
  methods: {
    editCustomerApi(){
     // console.log(this.editCustomer);
     
                        this.$store.dispatch('sendPutCustomer',this.editCustomer)
                         this.$notify({
          title: "Erfolg",
          message: "Erfolg",
          type: "success",
            duration: 7000
        });
                        this.$refs['editCustomer2'].hide()
                   
   //  t
    },
    getActiveCustomer(){
this.$confirm('This will permanently delete the file. Continue?', 'Warning', {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancel',
          type: 'success'
        }).then(() => {
          putApi('orders',this.editCustomer.id,{ isActive : true }).then(result=>{
            // console.log(result);
            
            if(result.result==1){
              this.editCustomer.isActive=true
           this.$message({
            type: 'success',
            message: "Customer activated"
          });
            }
            if(result.result==0){
              this.$message({
            type: 'info',
            message: 'Active canceled'
          });          
            }
            
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Active canceled'
          });          
        });
    },
    deleteCustomer(){
        this.$confirm('Möchten Sie wirklich löschen?', 'Warning', {
          confirmButtonText: 'Ja',
          cancelButtonText: 'Nein',
          type: 'warning'
        }).then(() => {
          putApi('orders',this.editCustomer.id,{ isActive : false }).then(result=>{
            // console.log(result);
            
            if(result.result==1){
              this.editCustomer.isActive = false
           this.$message({
            type: 'success',
            message: 'Delete completed'
          });
            }
            if(result.result==0){
              this.$message({
            type: 'info',
            message: 'Delete canceled'
          });          
            }
            
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: 'Delete canceled'
          });          
        });
    },
    handleCheckAllChange(val) {
      this.selectedType == "Premium"
        ? (this.optionsSelect = cityOptionsPremium)
        : (this.optionsSelect = cityOptions);
      this.checkedCities = val ? this.optionsSelect : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.cities.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length;
    }
  },

};
</script>

<style>
.hoverEdit:hover {
  background: whitesmoke !important;
  color: rgb(255, 191, 0) !important;
  transition: 0.5s linear all !important;
  cursor: pointer;
}
.hoverDelete:hover {
  background: whitesmoke !important;
  color: red !important;
  transition: 0.5s linear all !important;
  cursor: pointer;
}
.hoverSuccess:hover {
  background: whitesmoke !important;
  color: green !important;
  transition: 0.5s linear all !important;
  cursor: pointer;
}
.transitionSuccess {
  transition: 0.5s linear all;
  animation: 2s linear opac;
}
.transition {
  transition: 0.5s linear all;
}
.pulse {
  animation: 1s pulse infinite linear;
}
@keyframes opac {
  0% {
    position: absolute;
    opacity: 0;
  }
  50% {
    position: absolute;
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes pulse {
  0% {
    padding: 5px 8px;
  }
  50% {
    padding: 10px 12px;
  }
  100% {
    padding: 5px 8px;
  }
}
</style>