import Vue from "vue";
import Vuex from "vuex";
import branch from "./modules/static";
import users from "./modules/users";
import permission from "./modules/permission";
import customers from "./modules/customers";
import trucks from "./modules/trucks";
import lager from "./modules/lager";
import  packageDescription from "./modules/packageDescription";

Vue.use(Vuex);

export const store = new Vuex.Store({
  actions :{
    initApp(VuxContext){
    return  VuxContext.dispatch('initUserTable').then(result=>{
        if(result=='success'){
         return VuxContext.dispatch('initCompanyTable').then(response=>{
            if(response == 'success'){
              return VuxContext.dispatch('initTarifeTable').then(res=>{
                if(res=='success'){
                        return 'success'
                }
                else {
                   return "err"
                }
          
              })
             
            }
            else{
              return 'err'
            }
          })
        
        }
      })
    }
  },
  modules: {
    branch,
    users,
    lager,
    trucks,
    customers,
    permission,
    packageDescription
  }
});
