<template>
  <div>
      <el-row>
          <el-col :sm="24" :xs="24" :md="8" class="p-1"></el-col>
          <el-col :sm="24" :xs="24" :md="8" >
              <div class="text-center border rounded shadow bg-light p-4">
                <h2 class="text-center">Re Passwort</h2>
        <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="newPass"  type="text" required />
        <label> New Password </label>
      
      </div>
    </div>
        <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model.lazy="reNewPass" :class="reNewPass==newPass?'border-success border-bottom ':'border-danger border-bottom animated shake'"  type="text" required />
        <label> Confirm New Password </label>
      
      </div>
    </div>
        <small class="text-muted">{{reNewPass==newPass?'':''}}</small>

        <div class="form-group text-right">
            <el-button @click="sendNewPassword()" type="primary" :disabled="reNewPass!=newPass">Speichern</el-button>
        </div>
              </div>
          </el-col>
      </el-row>
  </div>
</template>

<script>
import { putApi,postApiHelper,postApiCreateUser } from '../api/api'
import { mapGetters } from 'vuex'
import auth from '../mixins/auth'
export default {
    mixins:[auth],
data(){
    return{
        newPass:"",
        reNewPass:"",
        errPoint:[]
    }
},
computed:{
    ...mapGetters(['getActiveUser','getUserPassword'])
},
methods:{
    sendNewPassword(){
        this.errPoint=[]
         if(this.newPass==""){
            this.errPoint.push({err: "Eksik Yeni Şifre"})
        }
         if(this.reNewPass==""){
            this.errPoint.push({err: "Şifreler Uyuşmuyor"})
        }

        if(this.errPoint.length==0){
           postApiHelper('password',{ password : this.newPass }).then(result=>{
        // console.log(result.result);
           putApi('user',this.getActiveUser.id,{  password : result.result }).then(result=>{
        if(result.result==1){
           postApiCreateUser({ userId : this.getActiveUser.id ,password : this.newPass }).then(result=>{
             // console.log(result);
             setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Der Benutzer wurde erfolgreich aktualisiert.",
          type: "success"
        });
        })
           })
              
        }
      })
      })
        }
        else{
            for (let i = 0; i < this.errPoint.length; i++) {
                setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: this.errPoint[i].err,
          type: "success"
        });
        },i*50)
                
            }
    }
    }
},
mounted(){
    if(this.getActiveUser.isAdmin){
        this.$router.push('/index')
    }
}
}
</script>

