<template>
  <div class="p-3">
    <p>Kunden insgesamt : {{ tableFiltred.length }}</p>

    <el-table
      :row-click="toggleRowSelection"
      class="border shadow p-3"
      :data="tableFiltred"
      :row-class-name="tableRowClassName"
      :default-sort="{ prop: 'setupDate', order: 'descending' }"
      style="width: 100%"
    >
      <el-table-column prop="customerName" label="Vorname" width="120px" sortable> </el-table-column>
      <el-table-column prop="customerLastname" label="Nachname" width="100px"> </el-table-column>
      <el-table-column
        prop="customerAddress"
        label="Straße"
        width="380px"
      >
      </el-table-column>
      <el-table-column prop="zipCode" label="PLZ">
        <template slot-scope="scope">
          {{ scope.row.zipCode }}
        </template>
      </el-table-column>
      <el-table-column prop="setupDate" label="Datum" width="100px">
        <template slot-scope="scope">
          <el-popover placement="right" width="600" trigger="click">
            <div class="bg-dark p-3 text-white">
              <p class="text-center ">
                <strong style="font-weight:700"
                  >Datum und Zeit ändern für
                  <span class="text-danger"
                    >BelegID : {{ editCustomer.beleg }}</span
                  ></strong
                >
              </p>
              <hr />
              <p class="text-center">

                <el-date-picker
                  v-model="datePicker"
                  type="date"
                  placeholder="Pick a day"
                  :picker-options="pickerOptions"
                >
                </el-date-picker>
                <strong style="font-weight:700" class="ml-3"
                  >Datum : {{editCustomer.setupDate }} </strong
                >
              </p>
              <p class="mt-3 text-center">
                <el-checkbox
                  @change="expressControl"
                  v-model="editCustomer.isExpress"
                  class="text-white"
                  border
                  >Expresslieferung</el-checkbox
                >
              </p>
              <div class="form-group  text-center p-2 ">
                <label>
                  <strong style="font-weight:bold" class="mr-3"
                    >Uhrzeit :</strong
                  >
                </label>
                <el-select  v-model="editSetupTime" placeholder="Select">
                  <el-option
                    v-for="item in resultOptions"
                    :key="item.value"
                    :label="item.label"
                    :disabled="item.disabled"
                    :value="item.value"
                  ></el-option>
                </el-select>
              </div>
              <el-button class="w-100" @click="sendEditCustomer()"
                >Weiter</el-button
              >
            </div>
            <p class="animePdf" @click="editDatum(scope.row)" slot="reference">{{
              scope.row.setupDate
            }}</p>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="setupTime" label="Zeit"> </el-table-column>
      <el-table-column prop="beleg" label="Beleg">
        <template slot-scope="scope">
          <p
            v-if="getActiveUser.isBelegAndern || getActiveUser.isStornieren"
           class="animePdf"
            v-b-modal.editCustomer2
            @click="editBelegNum(scope.row)"
            slot="reference"
            >{{ scope.row.beleg }}</p
          >
          <p v-else>{{scope.row.beleg}}</p>
        </template>
      </el-table-column>
      <el-table-column prop="lager" label="Lager" width="100px"> 
        <template slot-scope="scope">{{scope.row.isHaus && scope.row.isAus?'Mischlager':scope.row.isAus?'Außenlager':'Hauslager'}}</template>
      </el-table-column>
      <el-table-column prop="uUsername" label="User">
        <template slot-scope="scope">
          <p class="animePdf" >{{ scope.row.uUsername }}</p>
        </template>
      </el-table-column>
      <el-table-column prop="pdf" label="Pdf">
        <template slot-scope="scope">
          <i v-if="scope.row.id" @click="getPdfDataIndex(scope.row)" v-b-modal.exportPdf class="fas fa-file-pdf animePdf" style="font-size:32px"></i>
          <i  v-else class="fas fa-file-pdf text-light" style="font-size:32px"></i>
        </template>
      </el-table-column>
    </el-table>
    <edit-customer :editCustomer="editCustomer"></edit-customer>
    <export-pdf :pdfData="pdfData"></export-pdf>
  </div>
</template>

<script>


import editCustomer from "../Modals/editCustomerModalItems/editCustomer";
import exportPdf from "../exportPdf/exportPdf";
import { dateFormatter, dateFormatterElement, dataFormatterApiFormat } from "../../dateFormatter";
import { mapGetters } from "vuex";
import { putApi, getApi } from '../../api/api';
const options = [
  {
    value: "8-12",
    label: "8-12",
    disabled:false
  },
  {
    value: "10-14",
    label: "10-14",
    disabled:false

  },
  {
    value: "12-16",
    label: "12-16",
    disabled:false

  },
  {
    value: "14-18",
    label: "14-18",
    disabled:false

  },
   {
    value: "16-20",
    label: "16-20",
    disabled:true

  }
];
const optionsExpress = [
  {
    value: "10-11",
    label: "10-11",
    disabled:false
  },
  {
    value: "11-12",
    label: "11-12",
    disabled:false
  },
  {
    value: "12-13",
    label: "12-13",
    disabled:false
  },
  {
    value: "13-14",
    label: "13-14",
    disabled:true
  },
  {
    value: "14-15",
    label: "14-15",
    disabled:false
  },
  {
    value: "15-16",
    label: "15-16",
    disabled:false
  },
  {
    value: "16-17",
    label: "16-17",
    disabled:false
  }
];
export default {
  components: {
    editCustomer,
    exportPdf
  },
  computed: {
    ...mapGetters(["getCustomers","getActiveUser","getToday"])
  },
  watch: {
    getToday(){
      // console.log(this.getToday);
      
     
    },
    getCustomers(){
      this.$emit('data',this.getCustomers)
    },
    datePicker(event) {
      
      
      if(event!=null){
        if(event.getDay()==0){
            this.datePicker=null
            this.$notify({
          title: "Error",
          message: "Sonntags arbeiten wir nicht",
          type: "error"
        });
      }
     else{
        if(event!=null){
         this.editDatePicker = dateFormatterElement(event);
         let day = event.getDay()
        if(day==2 || day==4){
          options[4].disabled=false
        }
        else{
          options[4].disabled=true
          
        }
      }
      else{
           options[4].disabled=true
         
      }
  this.editCustomer.isExpress=false
     }
      }
      //this.editDatePicker = dateFormatter(date);
    },
    'editCustomer.isExpress'() {
      
      if (this.editCustomer.isExpress) {
        this.resultOptions = optionsExpress;
      } else {
        this.resultOptions = options;
      }
    }
  },
  data() {
    return {
      editSetupTime:null,
      pdfData:[],
      resultOptions: options,
      editDatePicker: "",
      index: null,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() < Date.now();
        },
        shortcuts: [
          {
            text: "Today",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nach einer Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      editCustomer: [],
      datePicker: "",
      revers: false
    };
  },
  props: ["tableFiltred"],
  methods: {  
       expressControl(data){
         let apiDate ;
         if(this.editDatePicker!=null){ apiDate = this.editDatePicker }
           else {  apiDate= dataFormatterApiFormat(this.editCustomer.setupDate)}
      if(data){
            if(this.editCustomer.companyId==1  || this.editCustomer.companyId==2){
          
              setTimeout(() => {
                  getApi('orders?filter=setupDate:"'+apiDate+'",companyId:'+this.editCustomer.companyId).then(res=>{
                    // console.log(res,apiDate,this.editCustomer.companyId);
        if(res.body!=null){
          res.body.forEach(body=>{
            // console.log(body.setupTime);
            if(body.setupTime=='10-11'){
              optionsExpress[0].disabled=true
            }
            if(body.setupTime=='11-12'){
              // console.log('girdik');
              
              optionsExpress[1].disabled=true
            }
            if(body.setupTime=='12-13'){
                 // console.log('girdik mi');
              optionsExpress[2].disabled=true
            }
            if(body.setupTime=='13-14'){
              optionsExpress[3].disabled=true
            }
            if(body.setupTime=='14-15'){
              optionsExpress[4].disabled=true
            }
            if(body.setupTime=='15-16'){
              optionsExpress[5].disabled=true
            }
            if(body.setupTime=='16-17'){
              optionsExpress[6].disabled=true
            }
            if(body.id == this.editCustomer.id){
                 for (let i = 0; i < optionsExpress.length; i++) {
          optionsExpress[i].disabled =false
          // console.log('girdik mi');
          
        }
            }
            
          })
        }
      })
              }, 500);
            }
            else {
                getApi('orders?filter=setupDate:"'+apiDate+'",companyId:3||companyId:4||companyId:5||companyId:6||companyId:7||companyId:8||companyId:9||companyId:10').then(res=>{
        if(res.body!=null){
          res.body.forEach(body=>{
            // console.log(body.setupTime);
            if(body.setupTime=='10-11'){
              optionsExpress[0].disabled=true
            }
            if(body.setupTime=='11-12'){
              // console.log('girdik');
              
              optionsExpress[1].disabled=true
            }
            if(body.setupTime=='12-13'){
              optionsExpress[2].disabled=true
            }
            if(body.setupTime=='13-14'){
              optionsExpress[3].disabled=true
            }
            if(body.setupTime=='14-15'){
              optionsExpress[4].disabled=true
            }
              if(body.setupTime=='15-16'){
              optionsExpress[5].disabled=true
            }
            if(body.setupTime=='16-17'){
              optionsExpress[6].disabled=true
            }
            
          })
        }
      })
            }
      }
      else {
        for (let i = 0; i < optionsExpress.length; i++) {
          // console.log('girdik mi');
          
          optionsExpress[i].disabled =false
          
        }
      }
    },
        tableRowClassName({row, rowIndex}) {

          
        if (!row.isActive) {
          return 'bg-warning100';
        }
        else
         return '';
      },
getPdfDataIndex(data){

    getApi('orders_SetupDefinition?filter=orderId:'+data.id).then(result=>{
      // console.log(result);
      
      this.pdfData = {...data, setupDefinition: result.body}
    })

  
},
    sendEditCustomer() {
      this.editCustomer.setupTime = this.editSetupTime
      // // // console.log(this.editCustomer,this.datePicker,this.editDatePicker,this.index);
      if(this.editDatePicker==null || this.editDatePicker=="" ){
        this.editCustomer.setupDate = this.editCustomer.setupDate
        this.editCustomer.setupDate = dataFormatterApiFormat(this.editCustomer.setupDate)
      }
      else{
      this.editCustomer.setupDate = this.editDatePicker;
     
      }
      putApi('orders',this.editCustomer.id, { setupDate : this.editCustomer.setupDate, setupTime : this.editCustomer.setupTime, isExpress : this.editCustomer.isExpress}).then(result=>{
        // // console.log(result);
         if(this.editDatePicker==null || this.editDatePicker=="" ){
        this.editCustomer.setupDate = this.editCustomer.setupDate
           this.editCustomer.setupDate = dateFormatter(this.editCustomer.setupDate)
      }
      else{
      this.editCustomer.setupDate = this.editDatePicker;
      this.editCustomer.setupDate = dateFormatter(this.editCustomer.setupDate)
      }
        this.tableFiltred[this.index] = this.editCustomer;
      this.$notify({
        title: "Erfolg",
        message: "Der Kunde wurde erfolgreich registriert.",
        type: "success"
      });
      this.visible = false;
        
      })
    },
    toggleRowSelection(row, column, event) {
      // // console.log(row, column, event);
    },
    formatter(row, column) {
      return row.address;
    },
    editBelegNum(index) {
      this.visible = true;
      

      this.editCustomer = index
      let same = this.editCustomer.setupDate.includes("-");
      if (same) {
        this.editCustomer.setupDate = dateFormatter(this.editCustomer.setupDate);
      }
    },
    editDatum(index) {
       if(this.getToday>13 && !this.getActiveUser.isAdmin){
        this.datePicker=null
        this. pickerOptions= {
        disabledDate(time) {
          return time.getTime() <= Date.now();
        },
        shortcuts: [
          {
            text: "Morgen",
            onClick(picker) {
                const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "zwei Tage später",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 2);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nach einer Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
      }
      this.visible = true;
      this.editSetupTime = index.setupTime
      this.editCustomer = index
      let same = this.editCustomer.setupDate.includes("-");
      if (same) {
        this.editCustomer.setupDate = dateFormatter(this.editCustomer.setupDate);
      }
      // // console.log(this.tableFiltred, this.editCustomer);
    }
  },
  mounted() {
    this.$emit("data", this.getCustomers);
    
  }
};
</script>
<style scoped>
.el-table th {
  background-color: #1672f3 !important;
  background-image: linear-gradient(315deg, #1672f3 0%, #7bb6fe 74%);
  color: white;
}
  .el-table .warning-row {
    background: oldlace;
  }
.table-bg {
  background-color: #1672f3 !important;
  background-image: linear-gradient(315deg, #1672f3 0%, #7bb6fe 74%);
  color: white;
}
.shadow {
  box-shadow: 2px 3px 3px 4px rgba(0, 0, 0, 0.301);
  border-radius: 3px;
  text-align: center !important;
}
a {
  color: #1672f3 !important;
}
a:hover {
  color: #002a65 !important;
}
.hover:hover {
  background: rgba(0, 0, 0, 0.05);
  cursor: pointer;
}
.animePdf{
  color : red;
  cursor: pointer;
}
.animePdf:hover{
  color : darkred;
  transition: 0.5s linear all;
}
.warning{background-color:#ffdc74!important}
</style>
