<template>
        <div v-if="isWhite!=null">
        <el-col :offset="2" :span="21">
          <div class="form-group border shadow p-3">
            <el-radio-group v-model="chooseLager">
              <el-radio label="Außenlager">Außenlager</el-radio>
              <el-radio label="Hauslager">Hauslager</el-radio>
            </el-radio-group>
            <div class="form-group mt-3">
              <el-checkbox v-model="OldAppliance" label="Altgerät" border></el-checkbox>
            </div>
            <div class="form-group mt-3" v-if="!expresslieferung">
              <label for>
                <strong style="font-weight:bold">Art auswählen :</strong>
              </label>
              <el-select @change="selectedTypeMethod" class="ml-3" v-model="selectedType" placeholder="Select">
                <el-option
                  v-for="item in optionsType"
                  :key="item.id"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </div>
          </div>
        </el-col>

        <el-col v-if="optionsPremium.length!=0 &&!loading" :offset="2" :span="21">
          <div class="form-group p-3 border shadow" >
            <div>
              <label for>
                <strong style="font-weight:bold">Optionen</strong>
              </label>
              <hr />
            </div>
            <div class="form-group">
              <el-checkbox
                :indeterminate="isIndeterminate"
                v-model="checkAll"
                @change="handleCheckAllChange"
              >Alles Überprüfen</el-checkbox>
              <div style="margin: 15px 0;"></div>
              <el-checkbox-group v-model="checkedCities" @change="handleCheckedCitiesChange">
                <el-checkbox v-for="opt in optionsPremium" :label="opt.id" :key="opt.id">{{opt.name}}</el-checkbox>
              </el-checkbox-group>
            </div>
          </div>
        </el-col>
        <el-col v-if="loading" class="text-center" :span="24">
              <div class="text-center">
        <div class="sk-circle">
  <div class="sk-circle1 sk-child"></div>
  <div class="sk-circle2 sk-child"></div>
  <div class="sk-circle3 sk-child"></div>
  <div class="sk-circle4 sk-child"></div>
  <div class="sk-circle5 sk-child"></div>
  <div class="sk-circle6 sk-child"></div>
  <div class="sk-circle7 sk-child"></div>
  <div class="sk-circle8 sk-child"></div>
  <div class="sk-circle9 sk-child"></div>
  <div class="sk-circle10 sk-child"></div>
  <div class="sk-circle11 sk-child"></div>
  <div class="sk-circle12 sk-child"></div>
</div>
    </div>
        </el-col>
      </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { getApi } from '../../../api/api';
const cityOptions = [
  "20 Sender einstellen",
  "TV Wandmontage inkl 20 Sender",
  "Tueranschlagwechsel bis 85cm"
];

export default {
  data() {
    return {
      form: {
        belegNo: "",
        name: "",
        lastname: "",
        address: "",
        plz: "",
        telefon: ""
      },
      optionsSelect: cityOptions,
      checkAll: false,
      checkAllPremium: false,
      checkedCities: [],
      checkedCitiesPrmeium: [],
      isIndeterminate: true,
      isIndeterminatePremium: true,
      chooseLager: "Mischlager",
      selectedTime: "",
      loading:false,
      OldAppliance: false,
      optionsPremium:[],
      selectedDate: "",
      selectedType: null,
      optionsType: [
        { value: "Standart", label: "Standart" },
        { value: "Komfort", label: "Komfort" },
        { value: "Premium", label: "Premium" },
        { value: "Techn. Hilfe", label: "Techn. Hilfe" }
      ],
      oldOptions:[],
      options: [
        {
          value: "8-12",
          label: "8-12"
        },
        {
          value: "10-14",
          label: "10-14"
        },
        {
          value: "12-16",
          label: "12-16"
        },
        {
          value: "14-18",
          label: "14-18"
        }
      ],
      value2: "",
      goodsType1: false,
      goodsType2: false,
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() <= Date.now();
        },
        shortcuts: [
          {
            text: "Heute",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "Morgen",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "nächste Woche",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() + 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      }
    };
  },
  computed:{
    ...mapGetters(['getTarifeTable'])
  },
 props:['isWhite','editCustomer','expresslieferung'],
    watch: {
             oldOptions(){
         this.$emit('oldOptions',this.oldOptions)
       },
    'editCustomer.isOldMachine'(){
      this.OldAppliance = this.editCustomer.isOldMachine
    },
      expresslieferung(data){
        if(data){
          this.selectedType=8
        }
      },
      optionsPremium(){
        this.checkedCities = []
      },

    selectedType(){
      // console.log(this.selectedType);
      this.$emit('selectedTypeItems', this.selectedType)
      
    },
        OldAppliance(){
      this.$emit('oldMach', this.OldAppliance)
    },
    chooseLager(){
      this.$emit('chooseLager',this.chooseLager)
    },
        checkedCitiesPrmeium(){
      this.$emit('checked', this.checkedCitiesPrmeium)
    },
    checkedCities(){
      this.$emit('checked',this.checkedCities)
    },
    value2(event) {
      // console.log(event);
      if (event != null) {
        this.selectedDate = dateFormatterElement(event);
        this.selectedDate = dateFormatter(this.selectedDate);
      } else {
        this.selectedDate = "";
      }
    }
  },
  methods: {
      selectedTypeMethod(data){
   
            this.loading=true
      this.$store.dispatch('initTarifeTableOptions',data).then(result=>{
              this.loading=false
        this.optionsPremium = result.body
      })
    },
    handleCheckAllChange(val) {

      this.checkedCities = val ? this.optionsPremium : [];
      this.isIndeterminate = false;
    },
    handleCheckedCitiesChange(value) {
      let checkedCount = value.length;
      this.checkAll = checkedCount === this.optionsPremium.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.optionsPremium.length;
    }
  },
  created(){
     // console.log('work',this.isWhite);
     // console.log(this.editCustomer);
     
         if(this.editCustomer!=null){
          this.OldAppliance = this.editCustomer.isOldMachine
         // if(this.getTarifeTable['id']==this.editCustomer.setupDefinitionId){ this.selectedType = this.getTarifeTable['id'].name }
          if(this.editCustomer.isHaus && this.editCustomer.isAus) {
          // console.log(this.chooseLager);
             this.chooseLager = 'Mischlager' }
             else {
                 if(this.editCustomer.isHaus) { 
          // console.log(this.chooseLager);
            this.chooseLager = 'Hauslager' }
           if(this.editCustomer.isAus) { 
          // console.log(this.chooseLager);
             this.chooseLager = 'Außenlager' }
             }
         
          // console.log(this.chooseLager);
          
        }
        this.optionsType=[]
        // this.getTarifeTable.forEach(res=>{
        //   if(this.isWhite==res.isWhite){
        //       if(res.id==this.editCustomer.setupDefinitionId){ this.selectedType = res.name }
                
        //       this.optionsType.push(res)
        //   }
        // })
   if(this.editCustomer!=null){
   this.editCustomer.id
   getApi('orders_SetupDefinition?filter=orderId:'+this.editCustomer.id).then(result=>{
     console.log(result);
     
     if(result.body!=null){
       this.selectedType = result.body[result.body.length-1].setupDefinitionId
      if(result.count!=1 &&  !result.body[result.body.length-1].isWhite){
         for (let i = 0; i < result.body.length-1; i++) {
        getApi('setupDefiniton?filter=parentId:'+this.selectedType).then(response=>{
            this.optionsPremium.push(response)
             setTimeout(() => {
               for (let i = 0; i < result.body.length-1; i++) {
        this.oldOptions.push(result.body[i].setupDefinitionId)
         this.checkedCities.push(result.body[i].setupDefinitionId)         
         this.loading=false
       }
         }, 250);
        })
         
       }
      }
     }
   })
   
   }
 
    this.getTarifeTable.forEach(result=>{
      if(!result.isWhite){
        this.optionsType.push(result)
      }
    })
   
  }
};
</script>

