<template>
  <div>
      <el-row class="mt-4">
          <el-col :xs="1" :sm="1" :md="6" >-</el-col>
          <el-col :xs="23" :sm="23" :md="12" class="border shadow bg-light rounded p-3">
<eralp-form @inputVModel="artikelNum=$event"> Artikel Suchen </eralp-form>
    <el-card
      shadow="always"
      class="my-2"
      v-for="(product, index) in filtredGetProduct"
      :key="index"
    >
      <div @click="addCountGoods(product)" style="cursor:pointer;">
        <el-row>
          <el-col :span="6"
            ><strong style="font-weight:700"
              >Artikel Num : {{ product.artikelNum }}
            </strong></el-col
          >
          <el-col :span="12">{{ product.name }}</el-col>
          <el-col :span="6">{{product['COUNT(productId)']}}</el-col>
        </el-row>
      </div>
    </el-card>
          </el-col>
        <b-modal size="lg" id="closeProduct" ref="closeProduct" hide-footer title="Warenausgang">
             <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="newGoodsItems.artikelNum" disabled  type="text" required />
        <label>  </label>
      
      </div>
    </div>
           <div class="eralp-form-control">
      <div class="eralp-input-container">
        <input v-model="newGoodsItems.name" disabled  type="text" required />
        <label>  </label>
      
      </div>
    </div>
          <label for="">Menge</label>
          <input type="number"  class="form-control" v-model="quantity">

          <div class="text-center my-3">
              <button @click="sendFreeProduct()" v-b-modal.freeProducts class="btn btn-outline-primary mx-3">Speichern</button>
              <button @click="$refs['closeProduct'].hide()" class="btn btn-outline-primary">Abbrechen</button>
          </div>
        <b-modal id="freeProducts" ref="freeProducts" hide-footer title="Free Artikel" size="xl">
<div class="my-2 text-right">
        <el-button  type="primary" @click="$refs['closeProduct'].hide()" round> <i class="fas fa-plus"></i>  Artikel Hinzufügen</el-button>
        <el-button  type="primary" v-b-modal.newCloseBelegNum round> <i class="fas fa-plus"></i>  Beleg Speichern</el-button>
</div>
{{branchId}} {{index}} 
                     <el-table

      class="border shadow p-3"
      :data="freeCloseProduct"
      style="width: 100%"
    >
      <el-table-column prop="artikelNum" label="Artikel Nr." sortable> </el-table-column>
      <el-table-column prop="name" label="Artikel"> </el-table-column>
      <el-table-column prop="quantity" label="Menge"> </el-table-column>
      <el-table-column prop="isEdit" label="Edit"> </el-table-column>

       </el-table>
       <b-modal id="newCloseBelegNum" ref="newCloseBelegNum" title="New Beleg Nr." hide-footer>
           <eralp-form @inputVModel="newBelegNumber=$event">Beleg Nr.</eralp-form>
           <el-card shadow="always" class="text-center">Filiale : {{branchName}}</el-card>

           <div class="my-3 text-center">
               <button @click="sendNewItemsForLagerInBranch()" class="btn btn-outline-primary btn-sm">Speichern</button>
           </div>
       </b-modal>
          </b-modal>
    </b-modal>
      </el-row>
  </div>
</template>

<script>
import eralpForm from '../eralpComponents/eralp-form'
import { mapGetters } from 'vuex';
export default {
components:{
    eralpForm
},
data(){
    return {
        artikelNum:null,
        newGoodsItems:[],
        freeCloseProduct:[],
        quantity:0
    }
},
props:['branchName','branchId','index'],
computed:{
    ...mapGetters(['getGoods','getBranchItemsQauntity','getActiveUser']),
        filtredGetProduct() {
      return this.getBranchItemsQauntity.filter(res => {
        return String(res.artikelNum).match(this.artikelNum);
      });
    },
    
},
methods:{
       addCountGoods(product) {
        this.quantity=0
      let count = 0;
      this.getGoods.forEach(response => {
        if (response.artikelNum == product.artikelNum) {
          count++;
        }
      });
      this.newGoodsItems = { ...product, count: count };
      if(this.getActiveUser.isAdmin){
        this.$refs.closeProduct.show()
      }
    },
        sendFreeProduct(){
        this.freeCloseProduct.push({...this.newGoodsItems, quantity: this.quantity})
        this.$bvModal.show('closeProduct')
    },
    sendNewItemsForLagerInBranch(){
      let date = new Date();
      let createdAt = String(date).substr(8,16)
        this.$store.dispatch('sendNewProblemItemsForLagerInBranch', { createdAt : createdAt, items : this.freeCloseProduct, index : this.index, branchId : this.branchId, belegNo : this.newBelegNumber}).then(result=>{
          setTimeout(() => {
                     this.$store.dispatch('sendBranchItemsQuantity',this.branchId).then(res=>{
        console.log('DEĞİŞTİ.');
              setTimeout(() => {
            this.$notify({
          title: "Erfolg",
          message: "Das Produkt wurde erfolgreich hinzugefügt.",
          type: "success"
        });
        }, 150);
          this.$refs['closeProduct'].hide();
          this.freeCloseProduct = []
      })
          }, 2000);
          if(result=='success'){
        
          }
        })
       
      

    }
},
watch:{
    artikelNum()
{
    if(this.artikelNum=="") this.artikelNum=null
},
quantity(){
  // console.log(this.quantity);
  
  if(this.newGoodsItems['COUNT(productId)'] < this.quantity){
    this.quantity = this.newGoodsItems['COUNT(productId)']
     setTimeout(() => {
          this.$notify({
          title: "Error",
          message: "Es können bis zu "+this.newGoodsItems['COUNT(productId)']+" Elemente geschrieben werden",
          type: "error",
            duration: 7000
        });
       }, 100);
  }
}},

}
</script>

